import { Component, OnInit, inject } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription, takeUntil } from 'rxjs';
import { UE } from 'src/app/models/UE';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { UeService } from 'src/app/services/ue/ue.service';

@Component({
  selector: 'app-update-ue',
  templateUrl: './update-ue.component.html',
  styleUrls: ['./update-ue.component.css']
})
export class UpdateUeComponent implements OnInit {
  hintColor = "red";
  updateForm: any;
  id!: number;
  //NOTE:INJECTIONS
  private fb = inject(FormBuilder);
  private service = inject(UeService);
  private toastr = inject(ToastrService);
  private route = inject(ActivatedRoute);
  private router = inject(Router);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    this.initializeForm();
    this.subscribeToRouteParams();
  }

  initializeForm() {
    this.updateForm = this.fb.group({
      code: [null, [Validators.required, Validators.minLength(4)]],
      label: [null, [Validators.required, Validators.minLength(4)]],
      totalCredit: [0],
    });
  }

  subscribeToRouteParams() {
    this.route.paramMap.pipe(takeUntil(this.destroyService.onDestroy$)).subscribe((value) => {
      this.id = Number(value.get('id'));
      this.service.getById(this.id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
        next: (res) => {
          this.fillForm(res);
        }, error: (error) => {
          console.log(error);
        }
      })
    });
  }

  fillForm(ue: UE) {
    this.updateForm.setValue({
      code: ue.code,
      label: ue.label,
      totalCredit: ue.totalCredit
    });
  }

  get fc() {
    return this.updateForm.controls;
  }

  get label(): FormControl {
    return this.updateForm.get('label') as FormControl;
  }

  update() {
    const ue = this.updateForm.value;
    this.service.putUpdate(ue, this.id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: () => {
          this.router.navigate(['/dashboard/ue']);
        },
        error: (error) => {
          console.log(error);
          if (error.status == 500) {
            this.toastr.error(error.error);
          }
        },
      }
    );
  }

}
