import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Observable, of } from 'rxjs';
import { LoginComponent } from 'src/app/login/login.component';
import { FiliereUeDto } from 'src/app/models/FiliereUeDto';
import { UE } from 'src/app/models/UE';
import { FiliereService } from 'src/app/services/filiere/filiere.service';
import { FiliereueService } from 'src/app/services/filiereue/filiereue.service';
import { UeService } from 'src/app/services/ue/ue.service';

@Component({
  selector: 'app-filiere-ue',
  templateUrl: './filiere-ue.component.html',
  styleUrls: ['./filiere-ue.component.css']
})
export class FiliereUeComponent implements OnInit {
  registerForm!: FormGroup;
  ues: UE[] = [];
  id!: number;
  ueControl: FormControl = new FormControl(null, Validators.required);
  displayUE!: string;
  filteredOptions!: Observable<UE[]>;
  filiereControl: FormControl = new FormControl(null, Validators.required);

  //NOTE: TABLE PART
  public uesT!: FiliereUeDto[];
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<FiliereUeDto>;
  displayedColumns: string[] = ['code','niveau','annee','semestre', 'action'];
  niveauM!: string;
  niveaux: string[] = ["LP", "MP", "BTS"];


  constructor(private fb: FormBuilder, private toastr: ToastrService, private ueService: UeService, private service: FiliereueService, private filiereService: FiliereService, private route: ActivatedRoute) {

  }

  ngOnInit(): void {
    this.registerForm = this.fb.group({
      ue: this.ueControl,
      filiere: this.filiereControl,
      niveau: ['LP', [Validators.required]],
      annee: [null, [Validators.required]],
      semestre: [null, [Validators.required]],
      active: [0],

    });
    this.getAllUes();
    this.ueControl.valueChanges
      .subscribe(val => {
        this.filteredOptions = of(this.findUes(val));
      });

    this.route.paramMap.subscribe((value) => {
      this.id = Number(value.get('id'));
      this.getFiliereByid(this.id);
      this.getAllFiliereUes(this.id);
    });
  }

  //NOTE: dynamique selection
  initializeUes() {
    this.filteredOptions = of(this.findUes(""))
  }

  getValueNiveau() {
    this.niveauM = this.registerForm.controls['niveau'].value;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  findUes(val: string): UE[] {
    return this.ues.filter(option =>
      option.code.toLowerCase().includes(val));
  }

  displayUe(value: any) {
    if (value != null) {
      return value.code;
    }
  }

  register() {
    const value = this.registerForm.value;
    console.log(value);
    
    this.service.postRegistration(value).subscribe({
      next: (response) => {
        this.registerForm = this.fb.group({
          ue: null,
          filiere: this.filiereControl,
          niveau: ['LP', [Validators.required]],
          annee: [null, [Validators.required]],
          semestre: [null, [Validators.required]],
          active: [1],
        });
        this.getAllFiliereUes(this.id);
      }, error: (response) => {
        this.registerForm = this.fb.group({
          ue: null,
          filiere: this.filiereControl,
          niveau: ['LP', [Validators.required]],
          annee: [null, [Validators.required]],
          semestre: [null, [Validators.required]],
          active: [1],
        });
        this.toastr.error(response.error)
      }
    })
  }

  //NOTE:FETCH DATA
  getAllUes() {
    this.ueService.getAllUes().subscribe(
      {
        next: (response) => {
          this.ues = response;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  getFiliereByid(id: number) {
    this.filiereService.getById(id).subscribe({
      next: (response) => {
        this.filiereControl.setValue(response);
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  getAllFiliereUes(id: number) {
    this.filiereService.getUesDtoById(id).subscribe(
      {
        next: (response) => {
          this.uesT = response;
          this.dataSource = new MatTableDataSource(this.uesT);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  delete(id: number) {
    this.service.delete(id).subscribe({
      next: (response) => {
        this.getAllFiliereUes(this.id);
      }, error: (error) => {
        console.log(error);
      }
    })
  }
}
