import { Component,OnInit,ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { Filiere } from 'src/app/models/Filiere';
import { FiliereService } from 'src/app/services/filiere/filiere.service';

@Component({
  selector: 'app-list-filiere',
  templateUrl: './list-filiere.component.html',
  styleUrls: ['./list-filiere.component.css']
})
export class ListFiliereComponent implements OnInit {
  public filieres!: Filiere[];
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<Filiere>;
  displayedColumns: string[] = ['id','name','action'];

  constructor(private service: FiliereService, private router: Router) {}
  ngOnInit(): void {
    this.getAllFilieres();
  }
  
  getAllFilieres() {
      this.service.getAll().subscribe(
        {
          next: (response) => {
            this.filieres = response;
            this.dataSource = new MatTableDataSource(this.filieres);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          },
          error: (error) => {
            console.log(error);
          },
        }
      );
  }

  getUes(id:number){
    this.router.navigate(["/dashboard/filiere/ues/"+id]);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  edit(id:number){
    this.router.navigate(["/dashboard/filiere/update/"+id]);
  } 

  delete(id:number){
    this.service.delete(id).subscribe({
      next:() =>{
        this.getAllFilieres();
      },error:(error) =>{
        console.log(error);
      }
    })
  }
}
