import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { LoginComponent } from './login/login.component';
import { AuthGuard } from './guards/auth.guard';
import { NoauthGuard } from './guards/noauth.guard';
import { ListUeComponent } from './components/admin/ue/list-ue/list-ue.component';
import { CreateUeComponent } from './components/admin/ue/create-ue/create-ue.component';
import { UpdateUeComponent } from './components/admin/ue/update-ue/update-ue.component';
import { ListMatiereComponent } from './components/admin/matiere/list-matiere/list-matiere.component';
import { CreateMatiereComponent } from './components/admin/matiere/create-matiere/create-matiere.component';
import { UpdateMatiereComponent } from './components/admin/matiere/update-matiere/update-matiere.component';
import { ListFiliereComponent } from './components/admin/filiere/list-filiere/list-filiere.component';
import { CreateFiliereComponent } from './components/admin/filiere/create-filiere/create-filiere.component';
import { UpdateFiliereComponent } from './components/admin/filiere/update-filiere/update-filiere.component';
import { ListClasseComponent } from './components/admin/classe/list-classe/list-classe.component';
import { CreateClasseComponent } from './components/admin/classe/create-classe/create-classe.component';
import { UpdateClasseComponent } from './components/admin/classe/update-classe/update-classe.component';
import { ListSemestreComponent } from './components/admin/semestre/list-semestre/list-semestre.component';
import { ListAnneeScolaireComponent } from './components/admin/annee_scolaire/list-annee-scolaire/list-annee-scolaire.component';
import { CreateAnneeScolaireComponent } from './components/admin/annee_scolaire/create-annee-scolaire/create-annee-scolaire.component';
import { UpdateAnneeScolaireComponent } from './components/admin/annee_scolaire/update-annee-scolaire/update-annee-scolaire.component';
import { ListProfComponent } from './components/admin/professeur/list-prof/list-prof.component';
import { CreateProfComponent } from './components/admin/professeur/create-prof/create-prof.component';
import { UpdateProfComponent } from './components/admin/professeur/update-prof/update-prof.component';
import { MatieresProfComponent } from './components/admin/professeur/matieres-prof/matieres-prof.component';
import { FiliereUeComponent } from './components/admin/filiere/filiere-ue/filiere-ue.component';
import { ListEtudiantComponent } from './components/admin/etudiant/list-etudiant/list-etudiant.component';
import { CreateEtudiantComponent } from './components/admin/etudiant/create-etudiant/create-etudiant.component';
import { UpdateEtudiantComponent } from './components/admin/etudiant/update-etudiant/update-etudiant.component';
import { InscriptionComponent } from './components/admin/etudiant/inscription/inscription.component';
import { InscriptionListeComponent } from './components/admin/etudiant/inscription-liste/inscription-liste.component';
import { NoteComponent } from './components/admin/note/note.component';
import { ClasseStudentsComponent } from './components/admin/classe/classe-students/classe-students.component';
import { ClasseMatieresComponent } from './components/admin/classe/classe-matieres/classe-matieres.component';
import { EtudiantNotesComponent } from './components/admin/etudiant/etudiant-notes/etudiant-notes.component';
import { FicheComponent } from './components/admin/report/fiche/fiche.component';
import { ReleveComponent } from './components/admin/report/releve/releve.component';

const routes: Routes = [
  // { path: '', component: DashboardComponent },
  { path: 'login', component: LoginComponent, canActivate: [NoauthGuard] },
  { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
  //NOTE UE ROUTES
  { path: 'dashboard/ue', component: ListUeComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/ue/create', component: CreateUeComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/ue/update/:id', component: UpdateUeComponent, canActivate: [AuthGuard] },
  //NOTE: Matiere Routes
  { path: 'dashboard/matiere', component: ListMatiereComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/matiere/create', component: CreateMatiereComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/matiere/update/:id', component: UpdateMatiereComponent, canActivate: [AuthGuard] },
  //NOTE: Filiere Routes
  { path: 'dashboard/filiere', component: ListFiliereComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/filiere/create', component: CreateFiliereComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/filiere/update/:id', component: UpdateFiliereComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/filiere/ues/:id', component: FiliereUeComponent, canActivate: [AuthGuard] },
  //NOTE: Classe Routes
  { path: 'dashboard/classe', component: ListClasseComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/classe/create', component: CreateClasseComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/classe/update/:id', component: UpdateClasseComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/classe/students/:id', component: ClasseStudentsComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/classe/matieres/:id', component: ClasseMatieresComponent, canActivate: [AuthGuard] },
  //NOTE: Professeur Routes
  { path: 'dashboard/professeur', component: ListProfComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/professeur/create', component: CreateProfComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/professeur/matieres/:id', component: MatieresProfComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/professeur/update/:id', component: UpdateProfComponent, canActivate: [AuthGuard] },
  //NOTE: Annee Scolaire Routes
  { path: 'dashboard/annee', component: ListAnneeScolaireComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/annee/create', component: CreateAnneeScolaireComponent, canActivate: [AuthGuard] },
  { path: 'dashboard/annee/update/:id', component: UpdateAnneeScolaireComponent, canActivate: [AuthGuard] },
  //NOTE: Semestre Routes
  { path: 'dashboard/semestre', component: ListSemestreComponent, canActivate: [AuthGuard] },
  //NOTE: Etudiant,Inscription
  {path:'dashboard/etudiant',component:ListEtudiantComponent,canActivate:[AuthGuard]},
  {path:'dashboard/etudiant/create',component:CreateEtudiantComponent,canActivate:[AuthGuard]},
  {path:'dashboard/etudiant/update/:matricule',component:UpdateEtudiantComponent,canActivate:[AuthGuard]},
  {path:'dashboard/etudiant/inscription/:matricule',component:InscriptionComponent,canActivate:[AuthGuard]},
  {path:'dashboard/etudiant/inscriptions',component:InscriptionListeComponent,canActivate:[AuthGuard]},
  //NOTE: MATIERES NOTE ROUTE
  {path:'dashboard/notes',component:NoteComponent,canActivate:[AuthGuard]},
  {path:'dashboard/notes/etudiants/:etudiant/:classe',component:EtudiantNotesComponent,canActivate:[AuthGuard]},
  //NOTE: ROUTES FOR REPORT
  {path:'dashboard/report/fichedenotes',component:FicheComponent,canActivate:[AuthGuard]},
  {path:'dashboard/report/relevedenotes',component:ReleveComponent,canActivate:[AuthGuard]},

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule { }
