import { Component, OnInit, OnDestroy, inject } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Observable, Subscription, of, takeUntil } from 'rxjs';
import { ClassWithFiliere } from 'src/app/models/ClassWithFiliere';
import { Classe } from 'src/app/models/Classe';
import { Filiere } from 'src/app/models/Filiere';
import { ClasseService } from 'src/app/services/classe/classe.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { FiliereService } from 'src/app/services/filiere/filiere.service';

@Component({
  selector: 'app-update-classe',
  templateUrl: './update-classe.component.html',
  styleUrls: ['./update-classe.component.css']
})
export class UpdateClasseComponent implements OnInit {
  hintColor = "red";
  updateForm!: FormGroup;
  filieres: Filiere[] = [];
  filiereControl: FormControl = new FormControl(null, Validators.required);
  filteredOptions!: Observable<Filiere[]>;
  niveaux: string[] = ["LP", "MP", "BTS"];
  niveauM!: String;
  id!: number;


  //NOTE: DEPENDENCY INJECTION
  private fb = inject(FormBuilder);
  private service = inject(ClasseService);
  private filiereService = inject(FiliereService);
  private toastr = inject(ToastrService);
  private route = inject(ActivatedRoute);
  private router = inject(Router);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    //NOTE: FORM REPRESENTATION
    this.initializeForm();

    this.route.paramMap.pipe(takeUntil(this.destroyService.onDestroy$))
    .subscribe((value) => {
      this.id = Number(value.get('id'));
      this.service.getById(this.id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
        next: (res) => {
          this.fillForm(res);
        }, error: () => {
          this.toastr.error("LOADING LIST FAILED", "ERROR");
        }
      })
    });

    this.getAllFilieres();

    //NOTE: Subscribe to value changes of this.filiereControl
    this.filiereControl.valueChanges
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(val => {
        this.filteredOptions = of(this.findFilieres(val));
      });
  }

  initializeForm() {
    this.updateForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(4)]],
      filiere: this.filiereControl,
      niveau: ['LP', [Validators.required]],
      annee: [null, [Validators.required]],
      effectif: [0]
    });
  }

  initializeFilieres() {
    this.filteredOptions = of(this.findFilieres(""))
  }

  fillForm(classeF: ClassWithFiliere) {
    this.niveauM = classeF.classe.niveau;
    this.updateForm.setValue({
      name: classeF.classe.name,
      filiere: classeF.filiere,
      niveau: classeF.classe.niveau,
      annee: classeF.classe.annee,
      effectif: classeF.classe.effectif
    });
  }

  findFilieres(val: string): Filiere[] {
    return this.filieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  getValueNiveau() {
    this.niveauM = this.updateForm.controls['niveau'].value;
  }

  displayFiliere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  getAllFilieres() {
    this.filiereService.getAll().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.filieres = response;
        },
        error: () => {
          this.toastr.error("LOADING LIST FAILED", "ERROR");
        },
      }
    );
  }

  //NOTESCONTROLES VALUES
  get name(): FormControl {
    return this.updateForm.get('name') as FormControl;
  }


  update() {
    const classe = this.updateForm.value;
    this.service.putUpdate(classe, this.id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: () => {
          this.router.navigate(['/dashboard/classe']);
        }, error: () => {
          this.toastr.error("DONNEES INVALIDE")
        }
      }
    );
  }

}
