import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { map, tap } from 'rxjs';
import { Professeur } from 'src/app/models/Professeur';
import { environment } from 'src/environments/environment.development';

@Injectable({
  providedIn: 'root'
})
export class ProfService {
  baseUrl = environment.apiURL + "prof";

  constructor(private http: HttpClient, private toastr: ToastrService) { }

  
  getAll() {
    return this.http.get<Professeur[]>(`${this.baseUrl}`);
  }

  getById(id:number) {
    return this.http.get<Professeur>(`${this.baseUrl}/${id}`);
  }

  postRegistration(register: Professeur) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.post<any>(`${this.baseUrl}` + "/register", JSON.stringify(register), httpOptions);
  }

  putUpdate(update: Professeur,id:number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.put<any>(`${this.baseUrl}` + "/update/"+id, JSON.stringify(update), httpOptions).pipe(
      tap(_ => this.toastr.success("MODIFICATION REUSSIE")),  
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  delete(id:number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.delete<any>(`${this.baseUrl}` + "/delete/"+id, httpOptions).pipe(
      tap(_ => this.toastr.success("SUPRESSION REUSSIE")),  
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }
}

