import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Classe } from 'src/app/models/Classe';
import { environment } from 'src/environments/environment.development';
import { map, tap } from 'rxjs';
import { ClassWithFiliere } from 'src/app/models/ClassWithFiliere';
import { MatiereUE } from 'src/app/models/MatiereUe';
import { UE } from 'src/app/models/UE';

@Injectable({
  providedIn: 'root'
})
export class ClasseService {
  baseUrl = environment.apiURL + "classe";

  constructor(private http: HttpClient, private toastr: ToastrService) { }

  
  getAll() {
    return this.http.get<ClassWithFiliere[]>(`${this.baseUrl}`+'/anneescolaire');
  }

  getById(id:number) {
    return this.http.get<ClassWithFiliere>(`${this.baseUrl}/${id}`);
  }

  getMatieresById(id:number) {
    return this.http.get<MatiereUE[]>(`${this.baseUrl}/matieres/${id}`);
  }
  
  getUesById(id:number) {
    return this.http.get<UE[]>(`${this.baseUrl}/ues/${id}`);
  }

  getByFiliereAnneeNiveau(id:number,annee:number,niveau:number) {
    return this.http.get<Classe[]>(`${this.baseUrl}/${id}/${annee}/${niveau}`);
  }

  postRegistration(register: Classe) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.post<any>(`${this.baseUrl}` + "/register", JSON.stringify(register), httpOptions).pipe(
      tap(_ => this.toastr.success("ENREGISTREMENT REUSSIE")),
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  putUpdate(update: Classe,id:number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.put<any>(`${this.baseUrl}` + "/update/"+id, JSON.stringify(update), httpOptions).pipe(
      tap(_ => this.toastr.success("MODIFICATION REUSSIE")),  
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  delete(id:number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.delete<any>(`${this.baseUrl}` + "/delete/"+id, httpOptions).pipe(
      tap(_ => this.toastr.success("SUPRESSION REUSSIE")),  
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

}
