import { Component, inject } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { takeUntil } from 'rxjs';
import { Etudiant } from 'src/app/models/Etudiant';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { EtudiantService } from 'src/app/services/etudiant/etudiant.service';
import { ImageService } from 'src/app/services/utils/image.service';

@Component({
  selector: 'app-update-etudiant',
  templateUrl: './update-etudiant.component.html',
  styleUrls: ['./update-etudiant.component.css']
})
export class UpdateEtudiantComponent {
  updateForm!: FormGroup;
  hintColor = "red";
  maxDate!: Date;
  maxDateAd!: Date;
  selectedFile: any = null;
  private fb = inject(FormBuilder);
  private service = inject(EtudiantService);
  private toastr = inject(ToastrService);
  private router = inject(Router);
  private route = inject(ActivatedRoute);
  private destroyService = inject(DestroyService);

  constructor() {
    const currentDate = new Date();
    const currentYear = currentDate.getFullYear();
    const currentMonth = currentDate.getMonth();
    const currentDay = currentDate.getDate();
    this.maxDate = new Date(currentYear - 8, currentMonth, currentDay);
    this.maxDateAd = new Date(currentYear, currentMonth, currentDay);
  }

  ngOnInit(): void {
    this.initializeForm();
    this.loadDataFromMatriculeParam();
  }

  initializeForm() {
    this.updateForm = this.fb.group({
      matricule: [null, [Validators.required]],
      name: [null, [Validators.required, Validators.minLength(4)]],
      prenoms: [null, [Validators.required, Validators.minLength(4)]],
      age: [0, [Validators.required, Validators.maxLength(120)]],
      telephone: [null, [Validators.required]],
      dateNaiss: [null, [Validators.required]],
      tuteur: [null, [Validators.required, Validators.minLength(4)]],
      tuteurTel: [null, [Validators.required]],
      dateAdmission: [null, [Validators.required]],
      nationalite: [null, [Validators.required]],
      sexe: [null, [Validators.required]],
      photo: [""],
    });
  }

  loadDataFromMatriculeParam() {
    this.route.paramMap.pipe(takeUntil(this.destroyService.onDestroy$)).subscribe((value) => {
      const matricule = value.get('matricule')?.toString() ?? "";
      this.service.getByMatricule(matricule).subscribe({
        next: (res) => {
          this.fillForm(res);
        }, error: (error) => {
          console.log(error);
        }
      })
    });
  }

  //NOTE :FORMCONTROL GETTEURS
  get matricule(): FormControl {
    return this.updateForm.get('matricule') as FormControl;
  }

  get name(): FormControl {
    return this.updateForm.get('name') as FormControl;
  }

  get photo(): FormControl {
    return this.updateForm.get('photo') as FormControl;
  }

  get prenoms(): FormControl {
    return this.updateForm.get('prenoms') as FormControl;
  }

  get age(): FormControl {
    return this.updateForm.get("age") as FormControl;
  }

  get dateNaiss(): FormControl {
    return this.updateForm.get("dateNaiss") as FormControl;
  }

  get telephone(): FormControl {
    return this.updateForm.get("telephone") as FormControl;
  }

  get tuteur(): FormControl {
    return this.updateForm.get("tuteur") as FormControl;
  }

  get tuteurTel(): FormControl {
    return this.updateForm.get("tuteurTel") as FormControl;
  }

  get dateAdmission(): FormControl {
    return this.updateForm.get("dateAdmission") as FormControl;
  }

  get nationalite(): FormControl {
    return this.updateForm.get("nationalite") as FormControl;
  }


  //NOTE:METHODES
  update() {
    const etudiant = this.updateForm.value;
    this.service.putUpdate(etudiant, etudiant.matricule).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: () => {
          this.router.navigate(['/dashboard/etudiant']);
        },
        error: (error) => {
          if (error.status == 500) {
            this.toastr.error(error.error);
          }
        },
      }
    );
  }

  fillForm(etudiant: Etudiant) {
    this.updateForm.setValue({
      matricule: etudiant.matricule,
      name: etudiant.name,
      prenoms: etudiant.prenoms,
      age: etudiant.age,
      telephone: etudiant.telephone,
      dateNaiss: etudiant.dateNaiss,
      tuteur: etudiant.tuteur,
      tuteurTel: etudiant.tuteurTel,
      dateAdmission: etudiant.dateAdmission,
      nationalite: etudiant.nationalite,
      sexe: etudiant.sexe,
      photo: etudiant.photo,
    });
  }

  //NOTE: CHECK NUMBER
  validateNumber(event: KeyboardEvent): void {
    const input = event.key;
    const isNumber = /^[0-9]$/.test(input);
    const isBackspace = input === "Backspace";
    const isPlus = input === "+";
    const isSpace = input === " ";
    if (!isNumber && !isBackspace && !isPlus && !isSpace) {
      event.preventDefault();
    }
  }

  onSelectedFile(event: any) {
    if (event.target.files) {
      const file = event.target.files[0];
      // const uniqueName = this.imageService.generateUniqueFileName(file.name);
      // this.updateForm.patchValue({photo:uniqueName});
    }
  }
}
