import { Component, OnInit, OnDestroy, inject } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription, takeUntil } from 'rxjs';
import { Annee } from 'src/app/models/Annee';
import { AnneeService } from 'src/app/services/annee/annee.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';

@Component({
  selector: 'app-update-annee-scolaire',
  templateUrl: './update-annee-scolaire.component.html',
  styleUrls: ['./update-annee-scolaire.component.css']
})
export class UpdateAnneeScolaireComponent implements OnInit{
  hintColor = "red";
  updateForm: any;
  id!: number;
  private destroyService= inject(DestroyService);
  private fb = inject(FormBuilder);
  private service = inject(AnneeService);
  private toastr = inject(ToastrService);
  private route = inject(ActivatedRoute);
  private router =inject(Router);

  
  ngOnInit(): void {
    this.updateForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(4)]],
      enabled: [0],
      dateDebut: [null, [Validators.required]],
      dateFin: [null, [Validators.required]]
    });
    this.route.paramMap.pipe(takeUntil(this.destroyService.onDestroy$)).subscribe((value) => {
      this.id = Number(value.get('id'));
      this.service.getById(this.id).subscribe({
        next: (res) => {
          this.fillForm(res);
        }, error: () => {
          this.toastr.error("LOADING DATA FAILED","ERROR");
        }
      })
    });
  }

  fillForm(annee:Annee) {
    this.updateForm.setValue({
      name: annee.name,
      enabled:annee.enabled,
      dateDebut:annee.dateDebut,
      dateFin:annee.dateFin
    });
  }

  get fc() {
    return this.updateForm.controls;
  }

  update(){
    const annee = this.updateForm.value;
    this.service.putUpdate(annee,this.id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: () => {
          this.router.navigate(['/dashboard/annee']);
        },
        error: (error) => {
          if (error.status == 500) {
            this.toastr.error(error.error);
          }
        },
      }
    );
  }

}
