import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Injectable, inject } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { map, tap } from 'rxjs';
import { Matiere } from 'src/app/models/Matiere';
import { UE } from 'src/app/models/UE';
import { environment } from 'src/environments/environment.development';

@Injectable({
  providedIn: 'root'
})
export class UeService {
  baseUrl = environment.apiURL + "ue";
  private http = inject(HttpClient);
  private toastr = inject(ToastrService);

  getAllUes() {
    return this.http.get<UE[]>(`${this.baseUrl}`);
  }

  getMatieresById(id:number) {
    return this.http.get<Matiere[]>(`${this.baseUrl}`+'/matieres/'+id);
  }

  getById(id: number) {
    return this.http.get<UE>(`${this.baseUrl}/${id}`);
  }

  postRegistration(register: UE) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.post<any>(`${this.baseUrl}` + "/register", JSON.stringify(register), httpOptions).pipe(
      tap(_ => this.toastr.success("ENREGISTREMENT REUSSIE")),
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  putUpdate(update: UE, id: number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.put<any>(`${this.baseUrl}` + "/update/" + id, JSON.stringify(update), httpOptions).pipe(
      tap(_ => this.toastr.success("MODIFICATION REUSSIE")),
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  delete(id: number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.delete<any>(`${this.baseUrl}` + "/delete/" + id, httpOptions).pipe(
      tap(_ => this.toastr.success("SUPRESSION REUSSIE")),
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }
}
