import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Inject, Injectable, inject } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { map, tap } from 'rxjs';
import { Annee } from 'src/app/models/Annee';
import { Semestre } from 'src/app/models/Semestre';
import { environment } from 'src/environments/environment.development';

@Injectable({
  providedIn: 'root'
})
export class AnneeService {
  baseUrl = environment.apiURL + "annee";

  private http = inject(HttpClient);
  private toastr= inject(ToastrService);
  
  getAll() {
    return this.http.get<Annee[]>(`${this.baseUrl}`);
  }

  getAllSemestres(){
    return this.http.get<Semestre[]>(`${this.baseUrl}`+'/semestres');
  }

  getById(id:number) {
    return this.http.get<Annee>(`${this.baseUrl}/${id}`);
  }

  postRegistration(register: Annee) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.post<any>(`${this.baseUrl}` + "/register", JSON.stringify(register), httpOptions).pipe(
      tap(_ => this.toastr.success("ENREGISTREMENT REUSSIE")),
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  putUpdate(update:Annee ,id:number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.put<any>(`${this.baseUrl}` + "/update/"+id, JSON.stringify(update), httpOptions).pipe(
      tap(_ => this.toastr.success("MODIFICATION REUSSIE")),  
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  activeAnnee(id:number){
    return this.http.get<Annee>(`${this.baseUrl}/activer/${id}`);
  }

  activeSemestre(id:number){
    return this.http.get<Annee>(`${this.baseUrl}/semestre/activer/${id}`);
  }

  delete(id:number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.delete<any>(`${this.baseUrl}` + "/delete/"+id, httpOptions).pipe(
      tap(_ => this.toastr.success("SUPRESSION REUSSIE")),  
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  getSemestres(){
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.get<Semestre[]>(`${this.baseUrl}`+'/semestres');
  }
}
