import { Component, inject } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Matiere } from 'src/app/models/Matiere';
import { MatiereUE } from 'src/app/models/MatiereUe';
import { UE } from 'src/app/models/UE';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { MatiereService } from 'src/app/services/matiere/matiere.service';
import { UeService } from 'src/app/services/ue/ue.service';

@Component({
  selector: 'app-update-matiere',
  templateUrl: './update-matiere.component.html',
  styleUrls: ['./update-matiere.component.css']
})
export class UpdateMatiereComponent {
  hintColor = "red";
  updateForm: any;
  ues!: UE[];
  id!: number;

  private fb = inject(FormBuilder);
  private service = inject(MatiereService);
  private ueService = inject(UeService);
  private toastr = inject(ToastrService);
  private router = inject(Router);
  private route = inject(ActivatedRoute);
  private destroyService = inject(DestroyService);


  ngOnInit(): void {
    this.initializeForm();
    this.getAllUes();
    this.subscribeToRouteParams();
  }

  subscribeToRouteParams() {
    this.route.paramMap.subscribe((value) => {
      this.id = Number(value.get('id'));
      this.service.getById(this.id).subscribe({
        next: (res) => {
          this.fillForm(res);
        }, error: (error) => {
          console.log(error);
        }
      })
    });
  }

  initializeForm() {
    this.updateForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(4)]],
      credit: [null, [Validators.required]],
      ue: [null, [Validators.required]]
    });
  }

  getAllUes() {
    this.ueService.getAllUes().subscribe(
      {
        next: (response) => {
          this.ues = response;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  //NOTES: getteurs
  get name(): FormControl {
    return this.updateForm.get("name") as FormControl;
  }

  get credit(): FormControl {
    return this.updateForm.get("credit") as FormControl;
  }

  get ue(): FormControl {
    return this.updateForm.get("ue") as FormControl;
  }

  fillForm(MatiereUE: MatiereUE) {
    this.updateForm.setValue({
      name: MatiereUE.matiere.name,
      credit: MatiereUE.matiere.credit,
      ue: MatiereUE.ue
    });
  }

  update() {
    const matiere = this.updateForm.value;
    this.service.putUpdate(matiere, this.id).subscribe(
      {
        next: () => {
          this.router.navigate(['/dashboard/matiere']);
        }, error: (error) => {
          console.log(error);
        }
      }
    );
  }

  validateNumber(event: KeyboardEvent): void {
    const input = event.key;
    const isNumber = /^[0-9]$/.test(input);
    const isBackspace = input === "Backspace";
    if (!isNumber && !isBackspace) {
      event.preventDefault();
    }
  }
}
