import { Component, ViewChild, inject, OnInit, OnDestroy } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription, takeUntil } from 'rxjs';
import { ClassWithFiliere } from 'src/app/models/ClassWithFiliere';
import { ClasseService } from 'src/app/services/classe/classe.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';

@Component({
  selector: 'app-list-classe',
  templateUrl: './list-classe.component.html',
  styleUrls: ['./list-classe.component.css']
})
export class ListClasseComponent implements OnInit {
  public classes!: ClassWithFiliere[];
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort!: MatSort;
  public dataSource!: MatTableDataSource<ClassWithFiliere>;
  displayedColumns: string[] = ['id', 'name', 'niveau', 'annee', 'effectif', 'filiere', 'action'];

  //NOTE: INJECTIONS
  private service = inject(ClasseService);
  private router = inject(Router);
  private toastr = inject(ToastrService);
  private destroyService = inject(DestroyService);



  ngOnInit(): void {
    this.getAllClasses();
  }

  getAllClasses() {
    this.service.getAll().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.classes = response;
          this.dataSource = new MatTableDataSource(this.classes);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: () => {
          this.toastr.error("LOADING LIST FAILED", "ERROR");
        },
      }
    );
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getStudents(id: Number) {
    this.router.navigate(["/dashboard/classe/students/" + id]);
  }

  getMatieres(id: number) {
    this.router.navigate(["/dashboard/classe/matieres/" + id]);
  }

  edit(id: number) {
    this.router.navigate(["/dashboard/classe/update/" + id]);
  }

  delete(id: number) {
    this.service.delete(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.getAllClasses();
      }, error: () => {
        this.toastr.error("SUPPRESSION FAILED", "ERROR");
      }
    })
  }

}
