import { Component, OnInit, inject } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Observable, of, takeUntil } from 'rxjs';
import { Classe } from 'src/app/models/Classe';
import { Etudiant } from 'src/app/models/Etudiant';
import { Filiere } from 'src/app/models/Filiere';
import { ClasseService } from 'src/app/services/classe/classe.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { EtudiantService } from 'src/app/services/etudiant/etudiant.service';
import { FiliereService } from 'src/app/services/filiere/filiere.service';
import { InscriptionService } from 'src/app/services/inscription/inscription.service';

@Component({
  selector: 'app-inscription',
  templateUrl: './inscription.component.html',
  styleUrls: ['./inscription.component.css']
})
export class InscriptionComponent implements OnInit {
  //NOTES:VARIALES
  hintColor = "red";
  registerForm!: FormGroup;
  public filieres: Filiere[] = [];
  public classes: Classe[] = [];
  matricule!: string;
  fullName!: string;
  id!: number;
  maxDate!: Date;
  anneeSelected!: number;
  niveaux: string[] = ["LP", "MP", "BTS"];
  niveauM!: string;
  //NOTE:FORMCONTROLS
  filiereControl: FormControl = new FormControl(null, Validators.required);
  filteredFilieres!: Observable<Filiere[]>;
  classeControl: FormControl = new FormControl(null, Validators.required);
  filteredClasses!: Observable<Classe[]>;
  etudiantControl: FormControl = new FormControl(null);

  //NOTE:INJECTIONS
  private toastr = inject(ToastrService);
  private filiereService = inject(FiliereService);
  private classeService = inject(ClasseService);
  private service = inject(InscriptionService);
  private route = inject(ActivatedRoute);
  private etudiantService = inject(EtudiantService);
  private destroyService = inject(DestroyService);
  private fb = inject(FormBuilder);


  constructor() {
    const currentDate = new Date();
    const currentYear = currentDate.getFullYear();
    const currentMonth = currentDate.getMonth();
    const currentDay = currentDate.getDate();
    this.maxDate = new Date(currentYear, currentMonth, currentDay);
  }

  ngOnInit(): void {
    this.initializeForm();
    this.loadDataFromMatriculeParam();
    this.getAllFilieres();
    this.subscribeToFormChanges();
  }

  initializeForm() {
    this.registerForm = this.fb.group({
      dateInscription: [null, [Validators.required]],
      typeInscription: [null, [Validators.required]],
      annee: [null, [Validators.required]],
      niveau: [null, [Validators.required]],
      classe: this.classeControl,
      etudiant: this.etudiantControl
    });
  }

  loadDataFromMatriculeParam() {
    this.route.paramMap.pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe((value) => {
        this.matricule = value.get('matricule')?.toString() ?? "";
        this.getEtudiantById();
      });
  }

  subscribeToFormChanges() {
    this.filiereControl.valueChanges
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(val => {
        this.filteredFilieres = of(this.findFilieres(val));
      });
    this.classeControl.valueChanges
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(val => {
        this.filteredClasses = of(this.findClasses(val));
      });
  }

  get dateInscription(): FormControl {
    return this.registerForm.get('dateInscription') as FormControl;
  }

  get typeInscription(): FormControl {
    return this.registerForm.get('typeInscription') as FormControl;
  }

  get annee(): FormControl {
    return this.registerForm.get('annee') as FormControl;
  }

  get niveau(): FormControl {
    return this.registerForm.get('niveau') as FormControl;
  }

  get filiere(): Filiere {
    return this.filiereControl.value;
  }

  get classe(): Classe {
    return this.classeControl.value;
  }

  onSelectedFiliere(id: number) {
    this.classeControl.setValue(null);
    if (this.registerForm.get('annee')?.value && this.registerForm.get('niveau')?.value) {
      this.getAllClasses(id);
    }
  }

  getEtudiantById() {
    this.etudiantService.getByMatricule(this.matricule)
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe({
        next: (res) => {
          this.fullName = res.name + " " + res.prenoms;
          this.etudiantControl.setValue(res);
        }, error: (error) => {
          console.log(error);
        }
      })
  }

  getAllFilieres() {
    this.filiereService.getAll()
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(
        {
          next: (response) => {
            this.filieres = response;
          },
          error: (error) => {
            console.log(error);
          },
        }
      );
  }

  checkAnne() {
    if (!this.registerForm.get('niveau')?.value) {
      this.toastr.error("CHOSISSEZ UN NIVEAU ")
    }
    else if (!this.registerForm.get('annee')?.value) {
      this.toastr.error("CHOSISSEZ L' ANNEE ")
    }
  }

  fetchClasses() {
    if (this.id && this.registerForm.get('annee')?.value && this.registerForm.get('niveau')?.value) {
      this.getAllClasses(this.id);
      this.classeControl.setValue("");
    }

  }

  getAllClasses(id: number) {
    this.id = id;
    const annee = this.registerForm.get('annee')?.value;
    const niveau = this.registerForm.get('niveau')?.value;
    this.classeService.getByFiliereAnneeNiveau(id, annee, niveau)
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(
        {
          next: (response) => {
            this.classes = response;
          },
          error: (error) => {
            console.log(error);
          },
        }
      )
  }

  //NOTE: FIND IN 
  findFilieres(val: string): Filiere[] {
    return this.filieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  findClasses(val: string): Classe[] {
    return this.classes.filter(option =>
      option.name.toLowerCase().includes(val));
  }


  //NOTE: INITIALIZE
  initializeFilieres() {
    this.filteredFilieres = of(this.findFilieres(""))
  }

  initializeClasses() {
    this.filteredClasses = of(this.findClasses(""))
  }

  // NOTE:DISPLAY VALUE IN INPUT
  displayFiliere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  displayClasse(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  getValueNiveau() {
    this.niveauM = this.registerForm.controls['niveau'].value;
  }

  inscription() {
    const inscripiton = this.registerForm.value;
    this.service.postRegistration(inscripiton)
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(
        {
          next: () => {
            this.registerForm.setValue({
              dateInscription: [null],
              typeInscription: [null],
              annee: [null],
              niveau: [null],
              classe: [null],
              etudiant: this.etudiantControl
            });
            this.getAllFilieres();
          }, error: (error) => {
            this.registerForm.setValue({
              dateInscription: [null],
              typeInscription: [null],
              annee: [null],
              niveau: [null],
              classe: [null],
              etudiant: this.etudiantControl
            });
            if (error.status == 500) {
              this.toastr.error(error.error);
            }
          }
        }
      );
  }
}
