import { Component } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Professeur } from 'src/app/models/Professeur';
import { ProfService } from 'src/app/services/prof/prof.service';

@Component({
  selector: 'app-update-prof',
  templateUrl: './update-prof.component.html',
  styleUrls: ['./update-prof.component.css']
})
export class UpdateProfComponent {
  hintColor = "red";
  updateForm: any;
  id!: number;
  maxDate!: Date;
  constructor(private fb: FormBuilder, private service: ProfService, private toastr: ToastrService, private route: ActivatedRoute,private router:Router) {
    const currentDate = new Date();
    const currentYear = currentDate.getFullYear();
    const currentMonth = currentDate.getMonth();
    const currentDay = currentDate.getDate();
    this.maxDate = new Date(currentYear, currentMonth, currentDay);
  }
  ngOnInit(): void {
    this.updateForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(4)]],
      prenoms: [null, [Validators.required, , Validators.minLength(4)]],
      telephone: [null, [Validators.required, Validators.pattern('^[0-9]*$')]],
      adresse: [null, [Validators.required]],
      email: [null, [Validators.required, Validators.email]],
      dateEmb: [null],
      sexe: [null,Validators.required]
    });
    this.route.paramMap.subscribe((value) => {
      this.id = Number(value.get('id'));
      this.service.getById(this.id).subscribe({
        next: (res) => {
          this.fillForm(res);
        }, error: (error) => {
          console.log(error);
        }
      })
    });
  }

  fillForm(prof:Professeur) {
    this.updateForm.setValue({
      name: prof.name,
      prenoms: prof.prenoms,
      telephone: prof.telephone,
      adresse: prof.adresse,
      email:prof.email,
      dateEmb: prof.dateEmb,
      sexe: prof.sexe
    });
  }

  get fc() {
    return this.updateForm.controls;
  }

  update(){
    const ue = this.updateForm.value;
    this.service.putUpdate(ue,this.id).subscribe(
      {
        next: (response) => {
          this.router.navigate(['/dashboard/professeur']);
        },
        error: (error) => {
          console.log(error);
          if (error.status == 500) {
            this.toastr.error(error.error);
          }
        },
      }
    );
  }

  validateNumber(event: KeyboardEvent): void {
    const input = event.key;
    const isNumber = /^[0-9]$/.test(input);
    const isBackspace = input === "Backspace";
    if (!isNumber && !isBackspace) {
      event.preventDefault();
    }
  }
}
