import { Component, Inject, OnInit, inject, OnDestroy } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { Observable, of, takeUntil } from 'rxjs';
import { Classe } from 'src/app/models/Classe';
import { Filiere } from 'src/app/models/Filiere';
import { Matiere } from 'src/app/models/Matiere';
import { Professeur } from 'src/app/models/Professeur';
import { EnseignerService } from 'src/app/services/enseigner/enseigner.service';
import { ProfService } from 'src/app/services/prof/prof.service';
import { MatDialogRef } from '@angular/material/dialog';
import { DestroyService } from 'src/app/services/destroy/destroy.service';

@Component({
  selector: 'app-prof-add-edit',
  templateUrl: './prof-add-edit.component.html',
  styleUrls: ['./prof-add-edit.component.css']
})
export class ProfAddEditComponent implements OnInit {
  classe!: Classe; // Ensure that the Classe and Matiere models are imported correctly
  matiere!: Matiere;
  registerForm!: FormGroup;
  professeurs: Professeur[] = [];
  profControl: FormControl = new FormControl(null, Validators.required);
  profOptions!: Observable<Professeur[]>;

  //NOTE:INJECTIONS
  private profService = inject(ProfService);
  private enseignerService = inject(EnseignerService);
  private toastr = inject(ToastrService);
  private dialogRef = inject(MatDialogRef<ProfAddEditComponent>);
  private fb = inject(FormBuilder);
  private destroyService = inject(DestroyService);
  constructor(@Inject(MAT_DIALOG_DATA) private data: any) {

  }

  ngOnInit(): void {
    // Assign the received data to component properties
    this.classe = this.data.classe;
    this.matiere = this.data.matiere;
    this.getAllProfesseurs();
    this.initializeForm();
    this.profControl.valueChanges.pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(val => {
        this.profOptions = of(this.findProfesseurs(val));
      });
  }

  initializeForm() {
    this.registerForm = this.fb.group({
      professeur: this.profControl,
      classe: this.classe,
      matiere: this.matiere
    });
  }

  initializeProfesseurs() {
    this.profOptions = of(this.findProfesseurs(""))
  }


  findProfesseurs(val: string): Professeur[] {
    return this.professeurs.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  displayProfesseurs(professeur: Professeur): string {
    if (professeur) {
      return `${professeur.name} ${professeur.prenoms}`;
    }
    return '';
  }


  getAllProfesseurs() {
    this.profService.getAll().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.professeurs = response;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  close() {
    this.dialogRef.close(true);
  }

  register() {
    const value = this.registerForm.value;
    this.enseignerService.postProfesseurRegistration(value).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.close();
      }, error: (error) => {
        this.toastr.error(error.error);
      }
    })
  }

}
