import { Component, ViewChild, inject, OnDestroy, OnInit } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSlideToggleChange } from '@angular/material/slide-toggle';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription, takeUntil } from 'rxjs';
import { Annee } from 'src/app/models/Annee';
import { AnneeService } from 'src/app/services/annee/annee.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';

@Component({
  selector: 'app-list-annee-scolaire',
  templateUrl: './list-annee-scolaire.component.html',
  styleUrls: ['./list-annee-scolaire.component.css']
})
export class ListAnneeScolaireComponent implements OnInit {

  public annees!: Annee[];
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<Annee>;
  displayedColumns: string[] = ['id', 'name', 'dateDebut', 'dateFin', 'enabled', 'action'];

  //NOTE:INJECTIONS
  private service = inject(AnneeService);
  private router = inject(Router);
  private toastr = inject(ToastrService);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    this.getAll();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAll() {
    this.service.getAll().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.annees = response;
          this.dataSource = new MatTableDataSource(this.annees);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: (error) => {
          this.toastr.error("LOADING LIST FAILED","ERROR");
        },
      }
    );
  }

  edit(id: number) {
    this.router.navigate(["/dashboard/annee/update/" + id]);
  }

  delete(id: number) {
    this.service.delete(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.getAll();
      }, error: (error) => {
        this.toastr.error("SUPPRESSION FAILED","ERROR");

      }
    });
  }

  changeActive( id: number) {
    this.service.activeAnnee(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(result => {
      this.getAll();
    });
  }

}
