import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Injectable, inject } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { map, tap } from 'rxjs';
import { NoteDto } from 'src/app/models/NoteDto';
import { NoteDtoProf } from 'src/app/models/NoteDtoProf';
import { environment } from 'src/environments/environment.development';

@Injectable({
  providedIn: 'root'
})
export class NoteService {
  baseUrl = environment.apiURL + "notes";
  private http = inject(HttpClient);
  private toastr = inject(ToastrService);

  getNotes(matiere: Number, classe: Number) {
    return this.http.get<NoteDto[]>(`${this.baseUrl}/${matiere}/${classe}`);
  }

  getEtudiantNotes(etudiant: String, classe: Number) {
    return this.http.get<NoteDtoProf[]>(`${this.baseUrl}/etudiant/${etudiant}/${classe}`);
  }

  postRegistration(register: NoteDto, matiere: Number, classe: Number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.post<any>(`${this.baseUrl}` + "/register" + `/${matiere}/${classe}`, JSON.stringify(register), httpOptions).pipe(
      tap(_ => this.toastr.success("ENREGISTREMENT REUSSIE")),
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }
}
