import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Observable, of } from 'rxjs';
import { Annee } from 'src/app/models/Annee';
import { Classe } from 'src/app/models/Classe';
import { Filiere } from 'src/app/models/Filiere';
import { Matiere } from 'src/app/models/Matiere';
import { NoteDto } from 'src/app/models/NoteDto';
import { UE } from 'src/app/models/UE';
import { ClasseService } from 'src/app/services/classe/classe.service';
import { FiliereService } from 'src/app/services/filiere/filiere.service';
import { NoteService } from 'src/app/services/note/note.service';
import { ReportService } from 'src/app/services/report/report.service';
import { UeService } from 'src/app/services/ue/ue.service';

@Component({
  selector: 'app-note',
  templateUrl: './note.component.html',
  styleUrls: ['./note.component.css']
})
export class NoteComponent implements OnInit {
  loadForm!: FormGroup;
  public filieres: Filiere[] = [];
  public classes: Classe[] = [];
  public ues: UE[] = [];
  public matieres: Matiere[] = [];
  idFiliere!: number;
  idMatiere!: number;
  idClasse!: number;
  anneeSelected!: number;
  niveaux: string[] = ["LP", "MP", "BTS"];
  niveauM!: string;
  coef!: number;

  //NOTE:FORM CONTROLS
  filiereControl: FormControl = new FormControl(null, Validators.required);
  filteredFilieres!: Observable<Filiere[]>;
  classeControl: FormControl = new FormControl(null, Validators.required);
  filteredClasses!: Observable<Classe[]>;
  anneeControl: FormControl = new FormControl(null, Validators.required);
  niveauControl: FormControl = new FormControl(null, Validators.required);
  ueControl: FormControl = new FormControl(null, Validators.required);
  filteredUes!: Observable<UE[]>;
  matiereControl: FormControl = new FormControl(null, Validators.required);
  filteredMatieres!: Observable<Matiere[]>;

  //NOTE:TABLES
  public notesDtos: NoteDto[] = [];
  isShow: boolean = false;
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<any>;
  displayedColumns: string[] = ['id', 'nom', 'prenoms', 'devoir', 'devoir1', 'moyenne', 'examen', 'moyenneS', 'coef', 'noteDefinitive', 'moyenneClasse', 'appreciations'];
  displayedHeaderColumns: string[] = ['id', 'nom', 'prenoms', 'devoir', 'moyenne', 'examen', 'moyenneS', 'coef', 'noteDefinitive', 'moyenneClasse', 'appreciations'];

  //NOTE:DATA INSERTED
  public noteForms!: FormGroup;
  public registerForm!: FormGroup;
  public formArray: FormGroup[] = [];


  constructor(private formBuilder: FormBuilder, private fb: FormBuilder, private toastr: ToastrService, private service: NoteService, private filiereService: FiliereService, private ueService: UeService, private classeService: ClasseService,private reportService:ReportService) { }
  
  ngOnInit(): void {
    this.loadForm = this.fb.group({
      classe: this.classeControl,
      matiere: this.matiereControl
    });

    this.getAllFilieres();
    //NOTE: RECHERCHE DANS AUTOCOMPLETE
    this.filiereControl.valueChanges
      .subscribe(val => {
        this.filteredFilieres = of(this.findFilieres(val));
      });
    this.classeControl.valueChanges
      .subscribe(val => {
        this.filteredClasses = of(this.findClasses(val));
      });
    this.ueControl.valueChanges.subscribe(val => {
      this.filteredUes = of(this.findUes(val));
    });
    this.matiereControl.valueChanges.subscribe(val => {
      this.filteredMatieres = of(this.findMatieres(val));
    });
  }

  //NOTE: HANDLING SELECTION
  onSelectedFiliere(id: number) {
    this.idFiliere = id;
    this.classeControl.setValue(null);
    if (this.anneeControl.value && this.niveauControl.value) {
      this.getAllClasses(id);
    }
  }

  onSelectedClasse(id: number) {
    this.ueControl.setValue(null);
    if (this.anneeControl.value && this.niveauControl.value) {
      this.getAllUes(id);
    }
  }

  onSelectedUe(id: number) {
    this.matiereControl.setValue(null);
    this.getAllMatieres(id);
  }

  //NOTE:GET DATA
  getAllFilieres() {
    this.filiereService.getAll().subscribe(
      {
        next: (response) => {
          this.filieres = response;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  getAllClasses(id: number) {
    this.classeService.getByFiliereAnneeNiveau(id, this.anneeControl.value, this.niveauControl.value).subscribe(
      {
        next: (response) => {
          this.classes = response;
        },
        error: (error) => {
          console.log(error);
        },
      }
    )
  }

  getAllUes(id: number) {
    this.classeService.getUesById(id).subscribe({
      next: (response) => {
        this.ues = response;
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  getAllMatieres(id: number) {
    this.ueService.getMatieresById(id).subscribe({
      next: (response) => {
        this.matieres = response;
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  getValueNiveau() {
    this.niveauM = this.niveauControl.value;
  }

  getNotes(idMatiere: number, idClasse: number) {
    this.idClasse = idClasse;
    this.idMatiere = idMatiere;
    this.service.getNotes(idMatiere, idClasse).subscribe({
      next: (value) => {
        this.notesDtos = value;
        //NOTE:CREATE FORM IN TABLE
        this.noteForms = this.formBuilder.group({
          notes: this.formBuilder.array(value)
        });
        this.formArray =[];
        this.notesDtos.forEach((value) => {
          this.formArray.push(this.createNoteFormGroup(value));
        })
      }, error(err) {
        console.log(err);
      },
    });
  }

  // NOTE:DISPLAY VALUE IN INPUT
  displayFiliere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  displayClasse(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  displayUe(value: any) {
    if (value != null) {
      return value.code;
    }
  }

  displayMatiere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  //NOTE: GETTEURS
  get filiere(): Filiere {
    return this.filiereControl.value;
  }

  get classe(): Classe {
    return this.classeControl.value;
  }

  get annee(): Annee {
    return this.anneeControl.value;
  }

  get niveau() {
    return this.niveauControl.value;
  }

  get ue(): UE {
    return this.ueControl.value;
  }

  checkAnne() {
    if (!this.loadForm.get('niveau')?.value) {
      this.toastr.error("CHOSISSEZ UN NIVEAU ")
    }
    else if (!this.loadForm.get('annee')?.value) {
      this.toastr.error("CHOSISSEZ L' ANNEE ")
    }
  }

  fetchClasses() {
    if (this.idFiliere && this.loadForm.get('annee')?.value && this.loadForm.get('niveau')?.value) {
      this.getAllClasses(this.idFiliere);
      this.classeControl.setValue("");
    }
  }

  //NOTE:CHARGER LES NOTES
  loadNotes() {
    this.isShow = true;
    const data = this.loadForm.value;
    this.coef = data.matiere.credit;
    this.noteForms = this.formBuilder.group({
      notes: this.formBuilder.array([])
    });
    this.getNotes(data.matiere.id, data.classe.id);
  }

  //NOTE: CHECK NUMBER
  validateNumber(event: KeyboardEvent): void {
    const input = event.key;
    const isNumber = /^[0-9]$/.test(input);
    const isBackspace = input === "Backspace";
    const isPlus = input === "+";
    const isSpace = input === " ";
    if (!isNumber && !isBackspace && !isPlus && !isSpace) {
      event.preventDefault();
    }
  }

  register() {
    this.formArray.forEach((value, index) => {
      this.service.postRegistration(value.value, this.idMatiere, this.idClasse).subscribe({
        next: (response) => {
          this.getNotes(this.idMatiere, this.idClasse);
        }, error: (error) => {
          console.log(error);
        }
      })
    })
  }

  get notes(): FormArray {
    return this.noteForms.get('notes') as FormArray;
  }

  createNoteFormGroup(note: NoteDto): FormGroup {
    return this.fb.group({
      id: [note.id],
      nom: [note.nom],
      prenoms: [note.prenoms],
      moyenne: [note.moyenne],
      moyenneClasse: [note.moyenneClasse],
      noteDefinitive: [note.noteDefinitive],
      appreciations: [note.appreciations],
      matricule: [note.matricule],
      devoirOne: [note.devoirOne, Validators.required],
      devoirTwo: [note.devoirTwo, Validators.required],
      examen: [note.examen, Validators.required],
    });
  }

  //NOTE: FIND IN 
  findFilieres(val: string): Filiere[] {
    return this.filieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  findClasses(val: string): Classe[] {
    return this.classes.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  findUes(val: string): UE[] {
    return this.ues.filter(option =>
      option.code.toLowerCase().includes(val));
  }

  findMatieres(val: string): Matiere[] {
    return this.matieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  //NOTE: INITIALIZE
  initializeFilieres() {
    this.filteredFilieres = of(this.findFilieres(""))
  }

  initializeClasses() {
    this.filteredClasses = of(this.findClasses(""))
  }

  initializeUes() {
    this.filteredUes = of(this.findUes(''));
  }

  initializeMatieres() {
    this.filteredMatieres = of(this.findMatieres(''));
  }

  //NOTE:TABLES
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  //NOTE:EXPORT NOTE TO PDF
  exportPdf(){
    this.reportService.getReportFicheComplet(this.idMatiere,this.idClasse,"pdf").subscribe({
      next:()=>{
        this.toastr.info("EXCEL WAS GENERATED");
      },error:(error)=>{
        console.log(error);
      }
    })
  }

  exportExcel(){
    this.reportService.getReportFicheComplet(this.idMatiere,this.idClasse,"excel").subscribe({
      next:()=>{
        this.toastr.info("PDF WAS GENERATED");
      },error:(error)=>{
        console.log(error);
      }
    })
  }
}
