import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Filiere } from 'src/app/models/Filiere';
import { ClasseService } from 'src/app/services/classe/classe.service';
import { FiliereService } from 'src/app/services/filiere/filiere.service';
import { Observable, map, of, startWith } from 'rxjs';
@Component({
  selector: 'app-create-classe',
  templateUrl: './create-classe.component.html',
  styleUrls: ['./create-classe.component.css']
})
export class CreateClasseComponent implements OnInit {
  hintColor = "red";
  registerForm!: FormGroup;
  filieres: Filiere[] = [];
  filiereControl: FormControl = new FormControl(null, Validators.required);
  filteredOptions!: Observable<Filiere[]>;
  niveaux: string[] = ["LP", "MP", "BTS"];
  niveauM!: string;
  // filiereM: String = "";
  //NOTE: DEPENDENCY INJECTION
  constructor(private fb: FormBuilder, private service: ClasseService, private filiereService: FiliereService, private toastr: ToastrService) {

  }
  ngOnInit(): void {
    //NOTE: FORM REPRESENTATION
    this.registerForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(4)]],
      filiere: this.filiereControl,
      niveau: ['LP', [Validators.required]],
      annee: [null, [Validators.required]],
      effectif: [0]
    });
    //NOTE: GET ALL FILIERES
    this.getAllFilieres();

    // Subscribe to value changes of this.filiereControl
    this.filiereControl.valueChanges
      .subscribe(val => {
        this.filteredOptions = of(this.findFilieres(val));
     });
  }

  initializeFilieres() {
    this.filteredOptions = of(this.findFilieres(""))
  }


  findFilieres(val: string): Filiere[] {
    return this.filieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  getValueNiveau() {
    this.niveauM = this.registerForm.controls['niveau'].value;
  }

  displayFiliere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  getAllFilieres() {
    this.filiereService.getAll().subscribe(
      {
        next: (response) => {
          this.filieres = response;
        },
        error: (error) => {
        },
      }
    );
  }

  //NOTESCONTROLES VALUES
  get name(): FormControl {
    return this.registerForm.get('name') as FormControl;
  }


  register() {
    const classe = this.registerForm.value;
    this.service.postRegistration(classe).subscribe(
      {
        next: (response) => {
          this.registerForm.setValue({
            name: "",
            filiere: null,
            niveau: 'LP',
            annee: null,
            effectif: 0
          });
          this.getAllFilieres();
        }, error: (error) => {
          this.toastr.error("DONNEES  INVALIDE")
        }
      }
    );
  }

}

