import { ChangeDetectorRef, Component, OnInit, ViewRef, inject } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Subject, takeUntil } from 'rxjs';
import { UE } from 'src/app/models/UE';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { MatiereService } from 'src/app/services/matiere/matiere.service';
import { UeService } from 'src/app/services/ue/ue.service';

@Component({
  selector: 'app-create-matiere',
  templateUrl: './create-matiere.component.html',
  styleUrls: ['./create-matiere.component.css']
})
export class CreateMatiereComponent implements OnInit {
  hintColor = "red";
  registerForm: any;
  ues!: UE[];

  //NOTE:INJECTIONS
  private fb = inject(FormBuilder);
  private service = inject(MatiereService);
  private ueService = inject(UeService);
  private destroyService = inject(DestroyService);

  //NOTE: SUBSCRIPTIONS



  ngOnInit(): void {
    this.initializeForm();
    this.getAllUes();
  }

  initializeForm() {
    this.registerForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(4)]],
      credit: [null, [Validators.required]],
      ue: [null, [Validators.required]]
    });
  }

  getAllUes() {
    this.ueService.getAllUes().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.ues = response;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  //NOTES: getteurs
  get name(): FormControl {
    return this.registerForm.get("name") as FormControl;
  }

  get credit(): FormControl {
    return this.registerForm.get("credit") as FormControl;
  }

  get ue(): FormControl {
    return this.registerForm.get("ue") as FormControl;
  }


  register() {
    const matiere = this.registerForm.value;
    this.service.postRegistration(matiere).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: () => {
          this.registerForm.setValue({
            name: "",
            credit: 0,
            ue: null,
          });
          this.getAllUes();
        }, error: (error) => {
          console.log(error);
        }
      }
    );
  }

  validateNumber(event: KeyboardEvent): void {
    const input = event.key;
    const isNumber = /^[0-9]$/.test(input);
    const isBackspace = input === "Backspace";
    if (!isNumber && !isBackspace) {
      event.preventDefault();
    }
  }

}
