import { DatePipe } from '@angular/common';
import { Component, LOCALE_ID, ViewChild, OnInit, OnDestroy, inject, ChangeDetectorRef, ViewRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { Professeur } from 'src/app/models/Professeur';
import { ProfService } from 'src/app/services/prof/prof.service';
import { registerLocaleData } from '@angular/common';
import localeFr from '@angular/common/locales/fr';
import { Subject, Subscription, takeUntil } from 'rxjs';
import { DestroyService } from 'src/app/services/destroy/destroy.service';

registerLocaleData(localeFr);
@Component({
  selector: 'app-list-prof',
  templateUrl: './list-prof.component.html',
  styleUrls: ['./list-prof.component.css'],
  providers: [DatePipe, { provide: LOCALE_ID, useValue: 'fr' }]
})
export class ListProfComponent implements OnInit {
  public profs!: Professeur[];
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<Professeur>;
  displayedColumns: string[] = ['id', 'name', 'prenoms', 'telephone', 'email', 'dateEmb', 'sexe', 'action'];

  //NOTE:INJECTIONS
  private service = inject(ProfService);
  private router = inject(Router);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    this.getAllUes();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAllUes() {
    this.service.getAll().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.profs = response;
          this.dataSource = new MatTableDataSource(this.profs);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  edit(id: number) {
    this.router.navigate(["/dashboard/professeur/update/" + id]);
  }

  delete(id: number) {
    this.service.delete(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.getAllUes();
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  getMatieres(id: number) {
    this.router.navigate(["/dashboard/professeur/matieres/" + id]);
  }

}
