import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Etudiant } from 'src/app/models/Etudiant';
import { NoteDtoProf } from 'src/app/models/NoteDtoProf';
import { EtudiantService } from 'src/app/services/etudiant/etudiant.service';
import { NoteService } from 'src/app/services/note/note.service';

@Component({
  selector: 'app-etudiant-notes',
  templateUrl: './etudiant-notes.component.html',
  styleUrls: ['./etudiant-notes.component.css']
})


export class EtudiantNotesComponent implements OnInit {

  //NOTE: DATAS
  etudiant!: Etudiant;
  idMatiere!: number;
  idClasse!: number;
  idEtudiant!: String;

  //NOTE:DATA INSERTED
  public noteForms!: FormGroup;
  public registerForm!: FormGroup;
  public formArray: FormGroup[] = [];

  //NOTE: Tables DATAS
  public notesDtos: NoteDtoProf[] = [];
  isShow: boolean = false;
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<any>;
  displayedColumns: string[] = ['id', 'professeur', 'matiere', 'devoir', 'devoir1', 'moyenne', 'examen', 'moyenneS', 'coef', 'noteDefinitive', 'moyenneClasse', 'appreciations'];
  displayedHeaderColumns: string[] = ['id', 'professeur', 'matiere', 'devoir', 'moyenne', 'examen', 'moyenneS', 'coef', 'noteDefinitive', 'moyenneClasse', 'appreciations'];
  
  
  constructor(private formBuilder: FormBuilder, private fb: FormBuilder, private etudiantService: EtudiantService, private route: ActivatedRoute, private toastr: ToastrService, private service: NoteService) {
   this.noteForms = this.formBuilder.group({
      notes: this.formBuilder.array([])
    });
  }

  
  ngOnInit(): void {
    this.route.paramMap.subscribe((value) => {
      this.idEtudiant = String(value.get('etudiant'));
      this.idClasse = Number(value.get('classe'));
      this.getAllNotes(this.idEtudiant, this.idClasse);
      this.etudiantService.getByMatricule(this.idEtudiant).subscribe({
        next: (res) => {
          this.etudiant = res;
        }, error: (error) => {
          console.log(error);
        }
      })
    });
  }

  //NOTE:TABLES
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  validateNumber(event: KeyboardEvent): void {
    const input = event.key;
    const isNumber = /^[0-9]$/.test(input);
    const isBackspace = input === "Backspace";
    const isPlus = input === "+";
    const isSpace = input === " ";
    if (!isNumber && !isBackspace && !isPlus && !isSpace) {
      event.preventDefault();
    }
  }



  get notes(): FormArray {
    return this.noteForms.get('notes') as FormArray;
  }

  getAllNotes(idEtudiant: String, idClasse: number) {

    this.idClasse = idClasse;
    this.idEtudiant = idEtudiant;
    this.service.getEtudiantNotes(idEtudiant, idClasse).subscribe({
      next: (value) => {
        this.noteForms = this.formBuilder.group({
          notes: this.formBuilder.array([])
        });
        this.notesDtos = value;
        //NOTE:CREATE FORM IN TABLE
        this.noteForms = this.formBuilder.group({
          notes: this.formBuilder.array(value)
        });
        this.formArray = [];
        
        this.notesDtos.forEach((value) => {
          this.formArray.push(this.createNoteFormGroup(value));
        })
      }, error(err) {
        console.log(err);
      },
    });
  }

  createNoteFormGroup(note: NoteDtoProf): FormGroup {
    return this.fb.group({
      id: [note.id],
      professeur: [note.professeur],
      matiereName: [note.matiereName],
      matiere: [note.idMatiere],
      moyenne: [note.moyenne],
      moyenneClasse: [note.moyenneClasse],
      noteDefinitive: [note.noteDefinitive],
      appreciations: [note.appreciations],
      matricule: [note.matricule],
      devoirOne: [note.devoirOne, Validators.required],
      devoirTwo: [note.devoirTwo, Validators.required],
      examen: [note.examen, Validators.required],
    });
  }

  exportExcel() {

  }

  exportPdf() {

  }

  register() {

  }
}
