import { ChangeDetectorRef, Component, OnInit, ViewChild, ViewRef, inject } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { MatiereUE } from 'src/app/models/MatiereUe';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { MatiereService } from 'src/app/services/matiere/matiere.service';

@Component({
  selector: 'app-list-matiere',
  templateUrl: './list-matiere.component.html',
  styleUrls: ['./list-matiere.component.css']
})
export class ListMatiereComponent implements OnInit {
  public matieres!: MatiereUE[];
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<MatiereUE>;
  displayedColumns: string[] = ['id', 'name', 'credit', 'ue', 'action'];

  //NOTE: INJECTIONS
  private service = inject(MatiereService);
  private router = inject(Router);
  private destroyService = inject(DestroyService);
  
  ngOnInit(): void {
    this.getAllMatieres();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAllMatieres() {
    this.service.getAll().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.matieres = response;
          console.log(response);
          this.dataSource = new MatTableDataSource(this.matieres);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  edit(id: number) {
    this.router.navigate(["/dashboard/matiere/update/" + id]);
  }

  delete(id: number) {
    this.service.delete(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.getAllMatieres();
      }, error: (error) => {
        console.log(error);
      }
    })
  }

}
