import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LoginService } from '../services/auth/login.service';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { DestroyService } from '../services/destroy/destroy.service';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css'],
})
export class LoginComponent implements OnInit {
    validateForm: any;
    hintColor: string = "red";
    //NOTE:INJECTIONS
    private loginService = inject(LoginService);
    private router = inject(Router);
    private fb = inject(FormBuilder);
    private toastr = inject(ToastrService);
    private destroyService = inject(DestroyService);

    ngOnInit() {
        this.validateForm = this.fb.group({
            username: [null, [Validators.required]],
            password: [null, [Validators.required]],
        });
    }

    login() {
        const username = this.validateForm.get(['username'])!.value;
        const password = this.validateForm.get(['password'])!.value;
        this.loginService.login(username,password)
        .pipe(this.destroyService.onDestroy$)
        .subscribe({
            next: (response: any) => {
                this.loginService.loginUser(response.body.token, response.body.name);
                this.router.navigate(['']);
            },
            error: (error:any) => {
                this.toastr.error("Echec d' authentification");
                console.log(error);
            }
        });
    }


    reset() {
        this.validateForm.get('username')?.reset();
        this.validateForm.get('password')?.reset();
    }

    get fc() {
        return this.validateForm.controls;
    }

}
