import { Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { InscriptionDto } from 'src/app/models/InscriptionDto';
import { InscriptionService } from 'src/app/services/inscription/inscription.service';

@Component({
  selector: 'app-inscription-liste',
  templateUrl: './inscription-liste.component.html',
  styleUrls: ['./inscription-liste.component.css']
})
export class InscriptionListeComponent {
  public inscriptions!: InscriptionDto[];
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<InscriptionDto>;
  displayedColumns: string[] = ['etudiant', 'date', 'type', 'filiere', 'classe', 'niveau', 'annee', 'action'];
  constructor(private service: InscriptionService, private router: Router) {

  }


  ngOnInit(): void {

    this.getAll();

  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAll() {
    this.service.getAllInscriptionsByYear().subscribe(
      {
        next: (response) => {
          this.inscriptions = response;
          this.dataSource = new MatTableDataSource(this.inscriptions);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  edit(id: number) {
    this.router.navigate(["/dashboard/etuiant/update/" + id]);
  }

  inscription(matricule: String) {
    this.router.navigate(["/dashboard/etudiant/inscription/" + matricule]);
  }

  delete(id: number) {
    this.service.delete(id).subscribe({
      next: (response) => {
        this.getAll();
      }, error: (error) => {
        console.log(error);
      }
    });
  }
}
