import { EtudiantService } from 'src/app/services/etudiant/etudiant.service';
import { Component, OnInit, ViewChild, inject, OnDestroy } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Observable, Subscription, of, takeUntil } from 'rxjs';
import { Annee } from 'src/app/models/Annee';
import { Classe } from 'src/app/models/Classe';
import { Etudiant } from 'src/app/models/Etudiant';
import { Filiere } from 'src/app/models/Filiere';
import { Matiere } from 'src/app/models/Matiere';
import { UE } from 'src/app/models/UE';
import { ClasseService } from 'src/app/services/classe/classe.service';
import { FiliereService } from 'src/app/services/filiere/filiere.service';
import { InscriptionService } from 'src/app/services/inscription/inscription.service';
import { NoteService } from 'src/app/services/note/note.service';
import { ReportService } from 'src/app/services/report/report.service';
import { UeService } from 'src/app/services/ue/ue.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
@Component({
  selector: 'app-releve',
  templateUrl: './releve.component.html',
  styleUrls: ['./releve.component.css']
})
export class ReleveComponent implements OnInit {

  releveForm!: FormGroup;
  public filieres: Filiere[] = [];
  public classes: Classe[] = [];
  idFiliere!: number;
  anneeSelected!: number;
  niveaux: string[] = ["LP", "MP", "BTS"];
  niveauM!: string;
  coef!: number;

  //NOTE:FORM CONTROLS
  filiereControl: FormControl = new FormControl(null, Validators.required);
  filteredFilieres!: Observable<Filiere[]>;
  classeControl: FormControl = new FormControl(null, Validators.required);
  filteredClasses!: Observable<Classe[]>;
  anneeControl: FormControl = new FormControl(null, Validators.required);
  niveauControl: FormControl = new FormControl(null, Validators.required);

  //NOTE:TABLE ETUDIANTS DATA
  etudiants: Etudiant[] = [];
  isShow: boolean = false;
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<Etudiant>;
  displayedColumns: string[] = ['id', 'matricule', 'name', 'prenoms', 'age', 'dateNaiss', 'telephone', 'action'];
  private inscriptionService = inject(InscriptionService);

  //NOTE:INJECTIONS
  private fb = inject(FormBuilder);
  private toastr = inject(ToastrService);
  private filiereService = inject(FiliereService);
  private classeService = inject(ClasseService);
  private reportService = inject(ReportService);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    this.initializeForm();
    this.getAllFilieres();
    this.subscribeToFormChanges();
  }

  initializeForm() {
    this.releveForm = this.fb.group({
      classe: this.classeControl,
    });
  }

  //NOTE: RECHERCHE DANS AUTOCOMPLETE
  subscribeToFormChanges() {
    this.filiereControl.valueChanges
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(val => {
        this.filteredFilieres = of(this.findFilieres(val));
      });
    this.classeControl.valueChanges
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(val => {
        this.filteredClasses = of(this.findClasses(val));
      });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  //NOTE: HANDLING SELECTION
  onSelectedFiliere(id: number) {
    this.idFiliere = id;
    this.classeControl.setValue(null);
    if (this.anneeControl.value && this.niveauControl.value) {
      this.getAllClasses(id);
    }
  }


  //NOTE:GET DATA
  getAllFilieres() {
    this.filiereService.getAll().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.filieres = response;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  getAllClasses(id: number) {
    this.classeService.getByFiliereAnneeNiveau(id, this.anneeControl.value, this.niveauControl.value)
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
        {
          next: (response) => {
            this.classes = response;
          },
          error: (error) => {
            console.log(error);
          },
        }
      )
  }

  getValueNiveau() {
    this.niveauM = this.niveauControl.value;
  }

  // NOTE:DISPLAY VALUE IN INPUT
  displayFiliere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  displayClasse(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  displayUe(value: any) {
    if (value != null) {
      return value.code;
    }
  }

  displayMatiere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  //NOTE: GETTEURS
  get filiere(): Filiere {
    return this.filiereControl.value;
  }

  get classe(): Classe {
    return this.classeControl.value;
  }

  get annee(): Annee {
    return this.anneeControl.value;
  }

  get niveau() {
    return this.niveauControl.value;
  }

  checkAnne() {
    if (!this.releveForm.get('niveau')?.value) {
      this.toastr.error("CHOSISSEZ UN NIVEAU ")
    }
    else if (!this.releveForm.get('annee')?.value) {
      this.toastr.error("CHOSISSEZ L' ANNEE ")
    }
  }

  fetchClasses() {
    if (this.idFiliere && this.releveForm.get('annee')?.value && this.releveForm.get('niveau')?.value) {
      this.getAllClasses(this.idFiliere);
      this.classeControl.setValue("");
    }
  }

  //NOTE: FIND IN 
  findFilieres(val: string): Filiere[] {
    return this.filieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  findClasses(val: string): Classe[] {
    return this.classes.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  //NOTE: INITIALIZE
  initializeFilieres() {
    this.filteredFilieres = of(this.findFilieres(""))
  }

  initializeClasses() {
    this.filteredClasses = of(this.findClasses(""))
  }

  exportExcelFiche() {
    const data = this.releveForm.value;
    this.reportService.getReportFiche(data.matiere.id, data.classe.id, "excel").pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.toastr.info("EXCEL WAS GENERATED");
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  //NOTE: CHARGER LES ETUDIANTS
  load() {
    this.isShow = true;
    const data = this.releveForm.get("classe")?.value;
    this.inscriptionService.getAllEtudiantsByClasse(data.id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.etudiants = response;
          this.dataSource = new MatTableDataSource(this.etudiants);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  //NOTE: REPORT RELEVE
  exportExcelReleve(matricule: String) {
    const data = this.releveForm.value;
    this.reportService.getReportReleve(matricule, data.classe.id, "excel").pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.toastr.info("PDF WAS GENERATED");
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  exportPdfReleve(matricule: String) {
    const data = this.releveForm.value;
    this.reportService.getReportReleve(matricule, data.classe.id, "pdf").pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.toastr.info("PDF WAS GENERATED");
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  exportExcelReleveClasse() { }

  exportPdfReleveClasse() {
    this.etudiants.forEach(element => {
      this.reportService.getReportReleve(element.matricule, this.classeControl.value.id, "pdf").pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
        next: () => {
          this.toastr.info("PDF WAS GENERATED");
        }, error: (error) => {
          console.log(error);
        }
      })
    });
  }

}
