import { Component, OnInit, inject } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { takeUntil } from 'rxjs';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { FiliereService } from 'src/app/services/filiere/filiere.service';

@Component({
  selector: 'app-create-filiere',
  templateUrl: './create-filiere.component.html',
  styleUrls: ['./create-filiere.component.css']
})
export class CreateFiliereComponent implements OnInit {
  hintColor = "red";
  registerForm!: FormGroup;

  //NOTE:INJECTIONS
  private fb = inject(FormBuilder);
  private service=inject(FiliereService);
  private toastr = inject(ToastrService);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    this.registerForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(4)]],
    });
  }

  get name(){
    return this.registerForm.get('name') as FormControl;
  }


  register() {
    const filiere = this.registerForm.value;
    this.service.postRegistration(filiere).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          console.log(response.status);
          this.registerForm.setValue({
            name: ""
          });
        },
        error: (error) => {
          if (error.status == 500) {
            this.toastr.error(error.error);
          }
        },
      }
    );
  }

}
