import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { map, tap } from 'rxjs';
import { Enseigner } from 'src/app/models/Enseigner';
import { EnseignerProf } from 'src/app/models/EnseignerProf';
import { environment } from 'src/environments/environment.development';

@Injectable({
  providedIn: 'root'
})
export class EnseignerService {
  baseUrl = environment.apiURL + "enseigner";

  constructor(private http: HttpClient, private toastr: ToastrService) { }

  getAll(id:number){
    return this.http.get<EnseignerProf[]>(`${this.baseUrl}`+'/professeur/'+id);
  }

  postRegistration(register: Enseigner) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.post<any>(`${this.baseUrl}` + "/register", JSON.stringify(register), httpOptions).pipe(
      tap(_ => this.toastr.success("ENREGISTREMENT REUSSIE")),
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  postProfesseurRegistration(register: Enseigner) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.post<any>(`${this.baseUrl}` + "/professeur/register", JSON.stringify(register), httpOptions).pipe(
      tap(_ => this.toastr.success("ENREGISTREMENT REUSSIE")),
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  delete(id:number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.delete<any>(`${this.baseUrl}` + "/delete/"+id, httpOptions).pipe(
      tap(_ => this.toastr.success("SUPRESSION REUSSIE")),  
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }
}
