import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { map, tap } from 'rxjs';
import { Etudiant } from 'src/app/models/Etudiant';
import { Inscription } from 'src/app/models/Inscription';
import { InscriptionDto } from 'src/app/models/InscriptionDto';
import { environment } from 'src/environments/environment.development';

@Injectable({
  providedIn: 'root'
})
export class InscriptionService {
  baseUrl = environment.apiURL + "inscripiton";

  constructor(private http: HttpClient, private toastr: ToastrService) {}

  postRegistration(register: Inscription) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.post<any>(`${this.baseUrl}` + "/register", JSON.stringify(register), httpOptions).pipe(
      tap(_ => this.toastr.success("ENREGISTREMENT REUSSIE")),
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }

  getAll() {
    return this.http.get<InscriptionDto[]>(`${this.baseUrl}`);
  }

  getAllInscriptionsByYear(){
    return this.http.get<InscriptionDto[]>(`${this.baseUrl}` +"/inscriptions/annee");
  }

  getAllEtudiantsByYear(){
    return this.http.get<Etudiant[]>(`${this.baseUrl}` +"/etudiants/annee");
  }

  getAllEtudiantsByClasse(id:Number){
    return this.http.get<Etudiant[]>(`${this.baseUrl}` +"/etudiants/classe/"+id);
  }

  delete(id:number) {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) }
    return this.http.delete<any>(`${this.baseUrl}` + "/delete/"+id, httpOptions).pipe(
      tap(_ => this.toastr.success("SUPRESSION REUSSIE")),  
      map((res: HttpResponse<any>) => {
        return res;
      })
    );
  }
}
