import { inject } from '@angular/core';
import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { takeUntil } from 'rxjs';
import { UE } from 'src/app/models/UE';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { UeService } from 'src/app/services/ue/ue.service';

@Component({
  selector: 'app-list-ue',
  templateUrl: './list-ue.component.html',
  styleUrls: ['./list-ue.component.css']
})
export class ListUeComponent implements OnInit {
  public ues!: UE[];
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<UE>;

  displayedColumns: string[] = ['id', 'code', 'label', 'totalCredit', 'action'];
  //NOTE:INJECTIONS
  private service = inject(UeService);
  private router = inject(Router);
  private destroyService = inject(DestroyService);
  ngOnInit(): void {
    this.getAllUes();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAllUes() {
    this.service.getAllUes().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.ues = response;
          this.dataSource = new MatTableDataSource(this.ues);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  edit(id: number) {
    this.router.navigate(["/dashboard/ue/update/" + id]);
  }

  delete(id: number) {
    this.service.delete(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.getAllUes();
      }, error: (error) => {
        console.log(error);
      }
    })
  }

}
