import { Subscription, takeUntil } from 'rxjs';
import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { AnneeService } from 'src/app/services/annee/annee.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';

@Component({
  selector: 'app-create-annee-scolaire',
  templateUrl: './create-annee-scolaire.component.html',
  styleUrls: ['./create-annee-scolaire.component.css']
})
export class CreateAnneeScolaireComponent   implements OnInit {
  hintColor = "red";
  registerForm!: FormGroup;


  //NOTE: DEPENDENCY INJECTION
  private fb = inject(FormBuilder);
  private service = inject(AnneeService);
  private toastr = inject(ToastrService);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    this.registerForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(4)]],
      enabled: [0],
      dateDebut: [null, [Validators.required]],
      dateFin: [null, [Validators.required]]
    });
  } 

  register() {
    const annee = this.registerForm.value;
    this.service.postRegistration(annee).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: () => {
          this.registerForm.setValue({
            name: "",
            enabled: false,
            dateDebut: null,
            dateFin: null
          });
        },
        error: (error) => {
            this.toastr.error(error.error);
        },
      }
    );
  }

  get name():FormControl{
    return this.registerForm.get('name')  as FormControl;
  }

  get dateDebut():FormControl{
    return this.registerForm.get("dateDebut") as FormControl;
  }

  get dateFin():FormControl{
    return this.registerForm.get('dateFin') as FormControl;
  }

  get enabled():FormControl{
    return this.registerForm.get('enabled') as FormControl;
  }

}
