import { Component, OnInit, ViewChild, inject } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Classe } from 'src/app/models/Classe';
import { Matiere } from 'src/app/models/Matiere';
import { MatiereUE } from 'src/app/models/MatiereUe';
import { ClasseService } from 'src/app/services/classe/classe.service';
import { InscriptionComponent } from '../../etudiant/inscription/inscription.component';
import { ProfAddEditComponent } from '../prof-add-edit/prof-add-edit.component';
import { MatiereService } from 'src/app/services/matiere/matiere.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { takeUntil } from 'rxjs';

@Component({
  selector: 'app-classe-matieres',
  templateUrl: './classe-matieres.component.html',
  styleUrls: ['./classe-matieres.component.css']
})
export class ClasseMatieresComponent implements OnInit {
  id!: number;
  matiere!: Matiere;
  classe!: Classe;
  registerForm!: FormGroup;
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<MatiereUE>;
  displayedColumns: string[] = ['id', 'name', 'credit', 'ue', 'assigner', 'professeur'];

  //NOTE:INJECTION
  private service = inject(ClasseService);
  private matiereService = inject(MatiereService);
  private toastr = inject(ToastrService);
  private router = inject(Router);
  private route = inject(ActivatedRoute);
  private dialog = inject(MatDialog);
  private destroyService = inject(DestroyService);


  ngOnInit(): void {
    this.route.paramMap.pipe(takeUntil(this.destroyService.onDestroy$)).subscribe((value) => {
      this.id = Number(value.get('id'));
      this.service.getById(this.id).subscribe({
        next: (response) => {
          this.classe = response.classe;
        }
      })
      this.getAllMatieres();
    });
  }

  openAddEditProfesseur(id: number) {
    this.matiereService.getById(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: (response) => {
        this.matiere = response.matiere;
        const dialogData = {
          classe: this.classe,
          matiere: response.matiere // Replace this with the actual selected matiere from the table if needed
        };
        const dialogRef = this.dialog.open(ProfAddEditComponent, {
          data: dialogData
        });

        dialogRef.afterClosed().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe((registrationSuccess: boolean) => {
          if (registrationSuccess) {
            this.getAllMatieres();
          }
        });
      }
    })
  }

  getAllMatieres() {
    this.service.getMatieresById(this.id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.dataSource = new MatTableDataSource(response);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: () => {
          this.toastr.error("LOADING LIST FAILED", "ERROR");
        },
      }
    );
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
}
