import { Component, OnInit, inject } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { EtudiantService } from 'src/app/services/etudiant/etudiant.service';
import { FileHandle } from '../../../../models/FileHandle';
import { DomSanitizer } from '@angular/platform-browser';
import { ImageService } from 'src/app/services/utils/image.service';

@Component({
  selector: 'app-create-etudiant',
  templateUrl: './create-etudiant.component.html',
  styleUrls: ['./create-etudiant.component.css']
})
export class CreateEtudiantComponent implements OnInit {
  registerForm!: FormGroup;
  hintColor = "red";
  maxDate!: Date;
  maxDateAd!: Date;
  selectedFile: any = null;

  //NOTE:INJECTIONS
  private fb = inject(FormBuilder);
  private service = inject(EtudiantService);
  private toastr = inject(ToastrService);

  constructor() {
    const currentDate = new Date();
    const currentYear = currentDate.getFullYear();
    const currentMonth = currentDate.getMonth();
    const currentDay = currentDate.getDate();
    this.maxDate = new Date(currentYear - 8, currentMonth, currentDay);
    this.maxDateAd = new Date(currentYear, currentMonth, currentDay);
  }

  ngOnInit(): void {
    this.registerForm = this.fb.group({
      matricule: [null, [Validators.required]],
      name: [null, [Validators.required, Validators.minLength(4)]],
      prenoms: [null, [Validators.required, Validators.minLength(4)]],
      age: [0, [Validators.required, Validators.maxLength(120)]],
      telephone: [null, [Validators.required]],
      dateNaiss: [null, [Validators.required]],
      tuteur: [null, [Validators.required, Validators.minLength(4)]],
      tuteurTel: [null, [Validators.required]],
      dateAdmission: [null, [Validators.required]],
      nationalite: [null, [Validators.required]],
      sexe: [null, [Validators.required]],
      photo: [""],
    });
  }

  //NOTE :FORMCONTROL GETTEURS
  get matricule(): FormControl {
    return this.registerForm.get('matricule') as FormControl;
  }

  get name(): FormControl {
    return this.registerForm.get('name') as FormControl;
  }

  get photo(): FormControl {
    return this.registerForm.get('photo') as FormControl;
  }

  get prenoms(): FormControl {
    return this.registerForm.get('prenoms') as FormControl;
  }

  get age(): FormControl {
    return this.registerForm.get("age") as FormControl;
  }

  get dateNaiss(): FormControl {
    return this.registerForm.get("dateNaiss") as FormControl;
  }

  get telephone(): FormControl {
    return this.registerForm.get("telephone") as FormControl;
  }

  get tuteur(): FormControl {
    return this.registerForm.get("tuteur") as FormControl;
  }

  get tuteurTel(): FormControl {
    return this.registerForm.get("tuteurTel") as FormControl;
  }

  get dateAdmission(): FormControl {
    return this.registerForm.get("dateAdmission") as FormControl;
  }

  get nationalite(): FormControl {
    return this.registerForm.get("nationalite") as FormControl;
  }


  //NOTE:METHODES
  register() {
    const etudiant = this.registerForm.value;
    this.service.postRegistration(etudiant).subscribe(
      {
        next: (response) => {
          this.registerForm.setValue({
            matricule: "",
            name: "",
            prenoms: "",
            age: 0,
            telephone: null,
            dateNaiss: null,
            tuteur: null,
            tuteurTel: null,
            dateAdmission: null,
            nationalite: null,
            sexe: null,
            photo: [""],
          });
        },
        error: (error) => {
          if (error.status == 500) {
            this.toastr.error(error.error);
          }
        },
      }
    );
  }

  //NOTE: CHECK NUMBER
  validateNumber(event: KeyboardEvent): void {
    const input = event.key;
    const isNumber = /^[0-9]$/.test(input);
    const isBackspace = input === "Backspace";
    const isPlus = input === "+";
    const isSpace = input === " ";
    if (!isNumber && !isBackspace && !isPlus && !isSpace) {
      event.preventDefault();
    }
  }

  onSelectedFile(event: any) {
    if (event.target.files) {
      // const file = event.target.files[0];
      // const uniqueName = this.imageService.generateUniqueFileName(file.name);
      // this.registerForm.patchValue({photo:uniqueName});
    }
  }
}
