import { Component, OnInit,inject } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { takeUntil } from 'rxjs';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { UeService } from 'src/app/services/ue/ue.service';

@Component({
    selector: 'app-create-ue',
    templateUrl: './create-ue.component.html',
    styleUrls: ['./create-ue.component.css']
})
export class CreateUeComponent implements OnInit {
    hintColor = "red";
    registerForm!: FormGroup;


    //NOTE: INJECTIONS
    private fb = inject(FormBuilder);
    private service = inject(UeService);
    private toastr = inject(ToastrService);
    private destroyService = inject(DestroyService);

    ngOnInit(): void {
        this.initializeForm();
    }

    initializeForm() {
        this.registerForm = this.fb.group({
            code: [null, [Validators.required, Validators.minLength(4)]],
            label: [null, [Validators.required, Validators.minLength(4)]],
            totalCredit: [0],
        });
    }

    register() {
        const ue = this.registerForm.value;
        this.service.postRegistration(ue).pipe(takeUntil(this.destroyService.onDestroy$))
        .subscribe(
            {
                next: (response) => {
                    console.log(response.status);
                    this.registerForm.reset({
                        totalCredit: 0
                    });
                },
                error: (error) => {
                    if (error.status == 500) {
                        this.toastr.error(error.error);
                    }
                },
            }
        );
    }

    get code(): FormControl {
        return this.registerForm.get('code') as FormControl;
    }

    get label(): FormControl {
        return this.registerForm.get('label') as FormControl;
    }

    get codeId(): String {
        return this.registerForm.get('code')?.value;
    }
}
