import { Component, OnInit,  inject } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import {  takeUntil } from 'rxjs';
import { ProfService } from 'src/app/services/prof/prof.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';

@Component({
  selector: 'app-create-prof',
  templateUrl: './create-prof.component.html',
  styleUrls: ['./create-prof.component.css']
})
export class CreateProfComponent implements OnInit {
  hintColor = "red";
  registerForm!: any;
  maxDate!: Date;

  //NOTE:INJECTIONS
  private fb = inject(FormBuilder);
  private service = inject(ProfService);
  private toastr = inject(ToastrService);
  private destroyService = inject(DestroyService);

  constructor() {
    const currentDate = new Date();
    const currentYear = currentDate.getFullYear();
    const currentMonth = currentDate.getMonth();
    const currentDay = currentDate.getDate();
    this.maxDate = new Date(currentYear, currentMonth, currentDay);
  }

  ngOnInit(): void {
    this.initializeForm();
  }

  initializeForm() {
    this.registerForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(3)]],
      prenoms: [null, [Validators.required, , Validators.minLength(4)]],
      telephone: [null, [Validators.required, Validators.pattern('^[0-9]*$')]],
      adresse: [null],
      email: [null, [Validators.email]],
      dateEmb: [null],
      sexe: [null, Validators.required]
    });
  }

  //NOTE : ENREGISTER LE PROFESSEUR
  register() {
    const professeur = this.registerForm.value;
    this.service.postRegistration(professeur)
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(
        {
          next: () => {
            this.toastr.success("PROFESSEUR ENREGISTER", "SUCCES");
            this.resetForm();
          },
          error: (error) => {
            if (error.status == 500) {
              this.toastr.error(error.error);
            }
          },
        }
      );
  }

  //NOTE : RESET FORM
  resetForm() {
    this.registerForm.reset();
  }


  //NOTE: GET FORMS INPUTS VALUES
  get fc() {
    return this.registerForm.controls;
  }

  //NOTE: CHECK NUMBER
  validateNumber(event: KeyboardEvent): void {
    const input = event.key;
    const isNumber = /^[0-9]$/.test(input);
    const isBackspace = input === "Backspace";
    const isPlus = input === "+";
    const isSpace = input === " ";
    if (!isNumber && !isBackspace && !isPlus && !isSpace) {
      event.preventDefault();
    }
  }

}
