import { Component, ViewChild, OnInit, OnDestroy, inject } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSlideToggleChange } from '@angular/material/slide-toggle';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subscription, takeUntil } from 'rxjs';
import { Semestre } from 'src/app/models/Semestre';
import { AnneeService } from 'src/app/services/annee/annee.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';

@Component({
  selector: 'app-list-semestre',
  templateUrl: './list-semestre.component.html',
  styleUrls: ['./list-semestre.component.css']
})
export class ListSemestreComponent implements OnInit{
  public semestres!: Semestre[];
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<Semestre>;
  displayedColumns: string[] = ['indice', 'status'];


  //NOTE: INJECTIONS
  private service = inject(AnneeService);
  private toastr = inject(ToastrService);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    this.getAll();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAll() {
    this.service.getAllSemestres().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.semestres = response;
          this.dataSource = new MatTableDataSource(this.semestres);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  changeActive(id: number) {
    this.service.activeSemestre(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: () => {
        this.toastr.success("ACTIVATE");
        this.getAll();
      }, error(message) {
        console.log( message);
      }
    });
  }
}
