import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Filiere } from 'src/app/models/Filiere';
import { FiliereService } from 'src/app/services/filiere/filiere.service';

@Component({
  selector: 'app-update-filiere',
  templateUrl: './update-filiere.component.html',
  styleUrls: ['./update-filiere.component.css']
})
export class UpdateFiliereComponent implements OnInit {
  hintColor = "red";
  updateForm: any;
  id!: number;
  constructor(private fb: FormBuilder, private service: FiliereService, private toastr: ToastrService, private route: ActivatedRoute,private router:Router) { }
  ngOnInit(): void {
    this.updateForm = this.fb.group({
      name: [null, [Validators.required, Validators.minLength(4)]],
    });
    this.route.paramMap.subscribe((value) => {
      this.id = Number(value.get('id'));
      this.service.getById(this.id).subscribe({
        next: (res) => {
          this.fillForm(res);
        }, error: (error) => {
          console.log(error);
        }
      })
    });
  }

  get fc() {
    return this.updateForm.controls;
  }

  fillForm(filiere:Filiere) {
    this.updateForm.setValue({
      name: filiere.name
    });
  }

  update() {
    const filiere = this.updateForm.value;
    this.service.putUpdate(filiere,this.id).subscribe(
      {
        next: (response) => {
          this.router.navigate(['/dashboard/filiere']);;
        },
        error: (error) => {
          console.log(error);
          if (error.status == 500) {
            this.toastr.error(error.error);
          }
        },
      }
    );
  }

}
