import { Component, OnInit, inject, OnDestroy } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Observable, of, takeUntil } from 'rxjs';
import { Annee } from 'src/app/models/Annee';
import { Classe } from 'src/app/models/Classe';
import { Filiere } from 'src/app/models/Filiere';
import { Matiere } from 'src/app/models/Matiere';
import { Notes } from 'src/app/models/Note';
import { UE } from 'src/app/models/UE';
import { ClasseService } from 'src/app/services/classe/classe.service';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { FiliereService } from 'src/app/services/filiere/filiere.service';
import { NoteService } from 'src/app/services/note/note.service';
import { ReportService } from 'src/app/services/report/report.service';
import { UeService } from 'src/app/services/ue/ue.service';

@Component({
  selector: 'app-fiche',
  templateUrl: './fiche.component.html',
  styleUrls: ['./fiche.component.css']
})
export class FicheComponent implements OnInit {

  public filieres: Filiere[] = [];
  public classes: Classe[] = [];
  public ues: UE[] = [];
  public matieres: Matiere[] = [];
  ficheForm!: FormGroup;
  idFiliere!: number;
  anneeSelected!: number;
  niveaux: string[] = ["LP", "MP", "BTS"];
  niveauM!: string;
  coef!: number;

  //NOTE:FORM CONTROLS
  filiereControl: FormControl = new FormControl(null, Validators.required);
  filteredFilieres!: Observable<Filiere[]>;
  classeControl: FormControl = new FormControl(null, Validators.required);
  filteredClasses!: Observable<Classe[]>;
  anneeControl: FormControl = new FormControl(null, Validators.required);
  niveauControl: FormControl = new FormControl(null, Validators.required);
  ueControl: FormControl = new FormControl(null, Validators.required);
  filteredUes!: Observable<UE[]>;
  matiereControl: FormControl = new FormControl(null, Validators.required);
  filteredMatieres!: Observable<Matiere[]>;

  //NOTE:INJECTIONS
  private fb = inject(FormBuilder);
  private toastr = inject(ToastrService);
  private filiereService = inject(FiliereService);
  private ueService = inject(UeService);
  private classeService = inject(ClasseService);
  private reportService = inject(ReportService);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    this.initializeForm();
    this.getAllFilieres();
    this.subscribeToFormChanges();
  }

  initializeForm() {
    this.ficheForm = this.fb.group({
      classe: this.classeControl,
      matiere: this.matiereControl
    });

  }
  //NOTE: RECHERCHE DANS AUTOCOMATIQUE
  subscribeToFormChanges() {
    this.filiereControl.valueChanges
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(val => {
        this.filteredFilieres = of(this.findFilieres(val));
      });
    this.classeControl.valueChanges
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(val => {
        this.filteredClasses = of(this.findClasses(val));
      });
    this.ueControl.valueChanges.pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(val => {
        this.filteredUes = of(this.findUes(val));
      });
    this.matiereControl.valueChanges.pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(val => {
        this.filteredMatieres = of(this.findMatieres(val));
      });
  }

  //NOTE: HANDLING SELECTION
  onSelectedFiliere(id: number) {
    this.idFiliere = id;
    this.classeControl.setValue(null);
    if (this.anneeControl.value && this.niveauControl.value) {
      this.getAllClasses(id);
      this.getAllUes(id);
    }
  }

  onSelectedUe(id: number) {
    this.matiereControl.setValue(null);
    this.getAllMatieres(id);
  }

  //NOTE:GET DATA
  getAllFilieres() {
    this.filiereService.getAll().pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(
        {
          next: (response) => {
            this.filieres = response;
          },
          error: (error) => {
            console.log(error);
          },
        }
      );
  }

  getAllClasses(id: number) {
    this.classeService.getByFiliereAnneeNiveau(id, this.anneeControl.value, this.niveauControl.value)
      .pipe(takeUntil(this.destroyService.onDestroy$))
      .subscribe(
        {
          next: (response) => {
            this.classes = response;
          },
          error: (error) => {
            console.log(error);
          },
        }
      )
  }

  getAllUes(id: number) {
    this.filiereService.getUesById(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: (response) => {
        this.ues = response;
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  getAllMatieres(id: number) {
    this.ueService.getMatieresById(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: (response) => {
        this.matieres = response;
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  getValueNiveau() {
    this.niveauM = this.niveauControl.value;
  }

  // NOTE:DISPLAY VALUE IN INPUT
  displayFiliere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  displayClasse(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  displayUe(value: any) {
    if (value != null) {
      return value.code;
    }
  }

  displayMatiere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  //NOTE: GETTEURS
  get filiere(): Filiere {
    return this.filiereControl.value;
  }

  get classe(): Classe {
    return this.classeControl.value;
  }

  get annee(): Annee {
    return this.anneeControl.value;
  }

  get niveau() {
    return this.niveauControl.value;
  }

  get ue(): UE {
    return this.ueControl.value;
  }

  checkAnne() {
    if (!this.ficheForm.get('niveau')?.value) {
      this.toastr.error("CHOSISSEZ UN NIVEAU ")
    }
    else if (!this.ficheForm.get('annee')?.value) {
      this.toastr.error("CHOSISSEZ L' ANNEE ")
    }
  }

  fetchClasses() {
    if (this.idFiliere && this.ficheForm.get('annee')?.value && this.ficheForm.get('niveau')?.value) {
      this.getAllClasses(this.idFiliere);
      this.classeControl.setValue("");
    }
  }

  //NOTE: FIND IN 
  findFilieres(val: string): Filiere[] {
    return this.filieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  findClasses(val: string): Classe[] {
    return this.classes.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  findUes(val: string): UE[] {
    return this.ues.filter(option =>
      option.code.toLowerCase().includes(val));
  }

  findMatieres(val: string): Matiere[] {
    return this.matieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  //NOTE: INITIALIZE
  initializeFilieres() {
    this.filteredFilieres = of(this.findFilieres(""))
  }

  initializeClasses() {
    this.filteredClasses = of(this.findClasses(""))
  }

  initializeUes() {
    this.filteredUes = of(this.findUes(''));
  }

  initializeMatieres() {
    this.filteredMatieres = of(this.findMatieres(''));
  }

  exportExcelFiche() {
    const data = this.ficheForm.value;
    this.reportService.getReportFiche(data.matiere.id, data.classe.id, "excel")
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
        next: () => {
          this.toastr.info("EXCEL WAS GENERATED");
        }, error: (error) => {
          console.log(error);
        }
      })
  }

  exportExcelCompletFiche() {
    const data = this.ficheForm.value;
    this.reportService.getReportFicheComplet(data.matiere.id, data.classe.id, "excel")
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
        next: () => {
          this.toastr.info("FICHE COMPLTE  WAS GENERATED IN EXCEL");
        }, error: (error) => {
          console.log(error);
        }
      })
  }

  exportPdfFiche() {
    const data = this.ficheForm.value;
    this.reportService.getReportFiche(data.matiere.id, data.classe.id, "pdf")
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
        next: () => {
          this.toastr.info("PDF WAS GENERATED");
        }, error: (error) => {
          console.log(error);
        }
      })
  }

  exportPdfCompletFiche() {
    const data = this.ficheForm.value;
    this.reportService.getReportFicheComplet(data.matiere.id, data.classe.id, "pdf")
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
        next: () => {
          this.toastr.info("FICHE COMPLTE  WAS GENERATED IN PDF");
        }, error: (error) => {
          console.log(error);
        }
      })
  }


}
