import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Notes } from 'src/app/models/Note';
import { NoteDto } from 'src/app/models/NoteDto';
import { environment } from 'src/environments/environment.development';

@Injectable({
  providedIn: 'root'
})
export class ReportService {
  baseUrl = environment.apiURL + "report";
  constructor(private http: HttpClient, private toastr: ToastrService) { }

  getReportFicheComplet(matiere:Number,classe:Number,format:String) {
    return this.http.get<NoteDto[]>(`${this.baseUrl}/${matiere}/${classe}/${format}`);
  }

  getReportFiche(matiere:Number,classe:Number,format:String) {
    return this.http.get<Notes[]>(`${this.baseUrl}/fiche/${matiere}/${classe}/${format}`);
  }

  getReportReleve(matricule:String,classe:Number,format:String) {
    return this.http.get<Notes[]>(`${this.baseUrl}/releve/${matricule}/${classe}/${format}`);
  }
}
