import { Component, OnInit, ViewChild, inject } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Observable, of, takeUntil } from 'rxjs';
import { Classe } from 'src/app/models/Classe';
import { EnseignerProf } from 'src/app/models/EnseignerProf';
import { Filiere } from 'src/app/models/Filiere';
import { Matiere } from 'src/app/models/Matiere';
import { UE } from 'src/app/models/UE';
import { DestroyService } from 'src/app/services/destroy/destroy.service';
import { EnseignerService } from 'src/app/services/enseigner/enseigner.service';
import { FiliereService } from 'src/app/services/filiere/filiere.service';
import { ProfService } from 'src/app/services/prof/prof.service';
import { UeService } from 'src/app/services/ue/ue.service';

@Component({
  selector: 'app-matieres-prof',
  templateUrl: './matieres-prof.component.html',
  styleUrls: ['./matieres-prof.component.css']
})
export class MatieresProfComponent implements OnInit {
  registerForm!: FormGroup;
  public filieres: Filiere[] = [];
  public classes: Classe[] = [];
  public ues: UE[] = [];
  public matieres: Matiere[] = [];
  public id!: number;
  fullName!: String;

  //NOTE: id pour selected
  public idUe!: number;

  // NOTE: Filter and FormControl
  filiereControl: FormControl = new FormControl(null, Validators.required);
  filteredFilieres!: Observable<Filiere[]>;
  classeControl: FormControl = new FormControl(null, Validators.required);
  filteredClasses!: Observable<Classe[]>;
  ueControl: FormControl = new FormControl(null, Validators.required);
  filteredUes!: Observable<UE[]>;
  matiereControl: FormControl = new FormControl(null, Validators.required);
  filteredMatieres!: Observable<Matiere[]>;
  profControl: FormControl = new FormControl(null);

  //NOTE:TABLE DATAS
  public enseigners!: EnseignerProf[];;
  @ViewChild(MatPaginator) paginator !: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  public dataSource!: MatTableDataSource<EnseignerProf>;
  displayedColumns: string[] = ['filiere', 'classe', 'matiere', 'action'];

  //NOTE:INJECTIONS
  private fb = inject(FormBuilder);
  private toastr = inject(ToastrService);
  private service = inject(ProfService);
  private filiereService = inject(FiliereService);
  private ueService = inject(UeService);
  private enseignerService = inject(EnseignerService);
  private route = inject(ActivatedRoute);
  private destroyService = inject(DestroyService);

  ngOnInit(): void {
    this.initializeForm();
    this.subscribeToRouteParams();
    this.getAllFilieres();
    this.subscribeToFormChanges();
  }
  
  //NOTE: INITIALZE THE FORM
  initializeForm() {
    this.registerForm = this.fb.group({
      filiere: this.filiereControl,
      classe: this.classeControl,
      ue: this.ueControl,
      matiere: this.matiereControl,
      professeur: this.profControl
    });
  }

  subscribeToFormChanges() {
    this.filiereControl.valueChanges
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(val => {
        this.filteredFilieres = of(this.findFilieres(val));
      });
    this.classeControl.valueChanges
      .pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(val => {
        this.filteredClasses = of(this.findClasses(val));
      });
    this.ueControl.valueChanges.pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(val => {
      this.filteredUes = of(this.findUes(val));
    });
    this.matiereControl.valueChanges.pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(val => {
      this.filteredMatieres = of(this.findMatieres(val));
    });
  }

  subscribeToRouteParams() {
    this.route.paramMap.pipe(takeUntil(this.destroyService.onDestroy$)).subscribe((value) => {
      this.id = Number(value.get('id'));
      this.getAllEnseigner(this.id);
      this.getProfesseurById(this.id);
    });
  }

  //NOTE:GET SELECTED DATA
  get filiere(): Filiere {
    return this.filiereControl.value;
  }
  get ue(): UE {
    return this.ueControl.value;
  }

  //NOTE: RECUPERER LES VALEURS A LA SELECTION
  onSelectedFiliere(id: number) {
    this.ueControl.setValue(null);
    this.matiereControl.setValue(null);
    this.classeControl.setValue(null);
    this.getAllClasses(id);
    this.getAllUes(id);
  }
  onSelectedUe(id: number) {
    this.matiereControl.setValue(null);
    this.getAllMatieres(id);
  }

  //NOTE:FETCH ALL DATAS
  getAllFilieres() {
    this.filiereService.getAll().pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.filieres = response;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  getAllClasses(id: number) {
    this.filiereService.getClassesById(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe(
      {
        next: (response) => {
          this.classes = response;
        },
        error: (error) => {
          console.log(error);
        },
      }
    );
  }

  getAllUes(id: number) {
    this.filiereService.getUesById(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: (response) => {
        this.ues = response;
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  getAllMatieres(id: number) {
    this.ueService.getMatieresById(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: (response) => {
        this.matieres = response;
      }, error: (error) => {
        console.log(error);
      }
    })
  }

  getProfesseurById(id: number) {
    this.service.getById(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: (response) => {
        this.profControl.setValue(response);
      }, error: (error) => {
        console.log(error);
      }
    });
  }

  //NOTE:ENREGISTRER
  register() {
    const register = this.registerForm.value;
    this.enseignerService.postRegistration(register).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: (response) => {
        console.log(response);
        this.registerForm = this.fb.group({
          filiere: null,
          classe: null,
          ue: null,
          matiere: null,
          professeur: this.profControl
        });
        this.getAllEnseigner(this.id);
      }, error: (error) => {
        this.toastr.error(error.error);
        this.registerForm = this.fb.group({
          filiere: null,
          classe: null,
          ue: null,
          matiere: null,
          professeur: this.profControl
        });
      }
    })
  }

  //NOTE: INITIALIZE
  initializeFilieres() {
    this.filteredFilieres = of(this.findFilieres(""))
  }

  initializeClasses() {
    this.filteredClasses = of(this.findClasses(""))
  }

  initializeUes() {
    this.filteredUes = of(this.findUes(''));
  }

  initializeMatieres() {
    this.filteredMatieres = of(this.findMatieres(''));
  }

  //NOTE: MAKE SEARCH IN
  findFilieres(val: string): Filiere[] {
    return this.filieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  findClasses(val: string): Classe[] {
    return this.classes.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  findUes(val: string): UE[] {
    return this.ues.filter(option =>
      option.code.toLowerCase().includes(val));
  }

  findMatieres(val: string): Matiere[] {
    return this.matieres.filter(option =>
      option.name.toLowerCase().includes(val));
  }

  // NOTE:DISPLAY VALUE IN INPUT
  displayFiliere(value: any) {
    if (value != null) {
      this.matieres = [];
      return value.name;
    }
  }

  displayClasse(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  displayUe(value: any) {
    if (value != null) {
      return value.code;
    }
  }

  displayMatiere(value: any) {
    if (value != null) {
      return value.name;
    }
  }

  //NOTE: TABLE PART

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAllEnseigner(id: number) {
    this.enseignerService.getAll(id).pipe(takeUntil(this.destroyService.onDestroy$)).subscribe({
      next: (response) => {
        this.enseigners = response;
        this.dataSource = new MatTableDataSource(this.enseigners);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
    })
  }

  delete(id: number) {
    this.enseignerService.delete(id);
  }


}
